<template>
  <div>
    <div class="header">
      <el-card
        style="
          width: 50%;
          margin-left: 15%;
          text-align: center;
          color: white;
          background-color: #1e1e1e;
        "
      >
        {{ title }}
      </el-card>
    </div>
    <div class="box">
      <div class="left">
        <el-card style="margin-top: 2%">
          <div class="ql-container ql-snow" style="border: none">
            <div class="ql-editor">
              <div id="detail"></div>
            </div>
          </div>
        </el-card>
        <el-card
          style="
            background-color: gray;
            margin-top: 2%;
            color: white;
            font-size: 10px;
          "
        >
          共 {{ total }} 条回复
        </el-card>
        <el-card
          style="margin-top: 2%"
          v-for="item in speakList"
          :key="item.id"
        >
          <div>
            <el-avatar :src="imgUrl" shape="square"></el-avatar>
            <span style="font-size: 10px; opacity: 0.5; margin-left: 4%"
              >{{ item.user.college }} · {{ item.user.major }} ·
              {{ item.user.username }} · {{ item.user.year }} · 发起于
              {{ item.blogSpeak.blogSpeakTime }}
            </span>
          </div>

          <div style="font-size: 13px; margin: 4%">
            {{ item.blogSpeak.blogSpeak }}
          </div>

          <el-card
            v-for="speakFor in item.blogSpeakSpeak"
            style="margin-top: 2%; background-color: #41464d; color: white"
            :key="speakFor.id"
          >
            <div>
              <el-avatar :src="imgUrl" shape="square"></el-avatar>
              <span style="font-size: 10px; opacity: 0.5; margin-left: 4%"
                >{{ item.users.college }} · {{ item.users.major }} ·
                {{ item.users.username }} · {{ item.users.year }} · 发起于
                {{ speakFor.blogTime }} · <span style="color: red">回复</span> ·
                {{ item.user.username }}
              </span>
            </div>

            <div style="font-size: 13px; margin: 4%">
              {{ speakFor.blogSpeakSpeak }}
            </div>

            <div
              style="display: flex; justify-content: flex-end; margin-top: 2%"
            >
              <el-button
                size="mini"
                @click="DeleteSpeakSpeak(speakFor.id)"
                v-if="item.users.id == id"
                >删除</el-button
              >
            </div>
          </el-card>

          <div style="display: flex; justify-content: flex-end; margin-top: 2%">
            <el-button
              size="mini"
              type="success"
              @click="changeVisible2(item.blogSpeak.id)"
              >回复</el-button
            >
            <el-button
              size="mini"
              @click="DeleteBlogSpeak(item.blogSpeak.id)"
              v-if="item.user.id == id"
              >删除</el-button
            >
          </div>
        </el-card>

        <div>
          <el-pagination
            :total="total"
            @current-change="currentChange"
            background
            style="margin-top: 5%; margin-bottom: 10%"
          ></el-pagination>
        </div>
      </div>
      <div class="right">
        <div id="sakana-c" style="margin-top: 10%"></div>
        <el-card style="background-color: #f7f8fa; margin-top: 12%">
          <div style="opacity: 0.7">
            <div>学院：{{ userInfo[3] }}</div>
          </div>
          <div style="opacity: 0.7; margin-top: 10%">
            <div>专业：{{ userInfo[1] }}</div>
          </div>
          <div style="opacity: 0.7; margin-top: 10%">
            <div>年级：{{ userInfo[2] }}</div>
          </div>
          <div style="opacity: 0.7; margin-top: 10%">
            <div>姓名：{{ userInfo[0] }}</div>
            <div></div>
          </div>
          <div style="opacity: 0.7; margin-top: 10%">
            <div>发表博客数量：{{ userInfo[4] }}</div>
            <div></div>
          </div>
          <div style="margin-top: 10%; opacity: 0.7">
            文章简介：<span style="text-indent: 2em; font-size: 10px">
              {{ introduce }}
            </span>
          </div>
          <div style="opacity: 0.7; margin-top: 10%; font-size: 10px">
            <div>发表时间：{{ blogTime | formdata }}</div>
          </div>
          <div style="opacity: 0.7; margin-top: 10%; font-size: 10px">
            <div>分类：{{ classify }}</div>
            <div></div>
          </div>
          <div style="opacity: 0.7; margin-top: 10%; font-size: 10px">
            <div>浏览人数：{{ love }}</div>
          </div>
        </el-card>
        <div
          style="margin-top: 21%; display: flex; justify-content: space-around"
        >
          <el-button
            size="mini"
            icon="el-icon-edit"
            type="success"
            @click="visible = true"
            >发布评论</el-button
          >
          <el-button
            icon="el-icon-bell"
            size="mini"
            style="
              border: 1px solid green;
              background-color: white;
              color: balck;
            "
            @click="Listen()"
            >接收动态</el-button
          >
        </div>
        <div id="sakana-b" style="margin-top: 100%"></div>
      </div>
    </div>
    <el-dialog
      :visible.sync="visible"
      title="发表评论"
      :before-close="beforeClose"
    >
      <el-input
        type="textarea"
        show-word-limit
        maxlength="1000"
        v-model="blogSpeak"
      ></el-input>
      <div
        style="display: flex; justify-content: space-around; margin-top: 10%"
      >
        <el-button @click="blogSpeak = ''">重置</el-button>
        <el-button
          @click="AddBlogSpeak()"
          style="background-color: #1e1e1e; color: white"
          >发表评论</el-button
        >
      </div>
    </el-dialog>

    <el-dialog
      :visible.sync="visible2"
      title="回复评论"
      :before-close="beforeClose2"
    >
      <el-input
        type="textarea"
        show-word-limit
        maxlength="1000"
        v-model="speakspeak"
      ></el-input>
      <div
        style="display: flex; justify-content: space-around; margin-top: 10%"
      >
        <el-button @click="speakspeak = ''">重置</el-button>
        <el-button
          @click="AddBlogSpeakSpeak()"
          style="background-color: #1e1e1e; color: white"
          >回复评论</el-button
        >
      </div>
    </el-dialog>

    <div class="backTopOne">
      <el-button
        icon="el-icon-caret-top"
        circle
        style="background-color: white; color: #409eff"
        @click="backTopOne()"
        v-show="scrollTop"
      ></el-button>
    </div>
    <div class="backTopTwo">
      <el-button
        icon="el-icon-caret-bottom"
        circle
        style="background-color: white; color: #409eff"
        @click="backTopTwo()"
        v-show="scrollTop"
      ></el-button>
    </div>
  </div>
</template>

<script>
import "sakana-widget/lib/index.css";
import SakanaWidget from "sakana-widget";
import axios from "axios";
export default {
  data() {
    return {
      uuid: "",
      id: "",
      imgUrl: require("../assets/Index/2020112022562230967.jpg"),
      blogSpeak: "",
      skip: 1,
      speakList: [],
      time: "",
      title: "",
      introduce: "",
      classify: "",
      blogTime: "",
      love: "",
      userInfo: [],
      visible: false,
      total: 10,
      scrollTop: false,
      speakspeak: "",
      visible2: false,
      blogSpeakId: "",
    };
  },
  filters: {
    formdata(timetemp) {
      const now = new Date(+timetemp).toLocaleString();
      return now;
    },
  },
  mounted() {
    this.GetBlogWithUserMessage();
    this.GetBlogDetail();
    this.GetBlogSpeak();
    this.GetBlogUserInfo();
    this.ListenBackTop();
    const takina = SakanaWidget.getCharacter("takina");
    takina.initialState = {
      ...takina.initialState,
      i: 0.001,
      d: 1,
    };
    SakanaWidget.registerCharacter("takina-slow", takina);
    new SakanaWidget().mount("#sakana-c");
    new SakanaWidget().setState({ i: 0.001, d: 1 }).mount("#sakana-b");
  },
  methods: {
    //删除回复的回复
    DeleteSpeakSpeak(e) {
      this.$axios.get("/Blog/DeleteSpeakSpeak?id=" + e).then((res) => {
        if (res.data.code == "1") {
          this.$message.success({ message: "删除成功" });
          this.GetBlogSpeak();
          this.GetBlogUserInfo();
        } else {
          this.$message.error({ message: "删除失败" });
        }
      });
    },
    //修改回复的改变
    changeVisible2(e) {
      this.visible2 = true;
      this.blogSpeakId = e;
    },
    //添加回复的回复
    AddBlogSpeakSpeak() {
      if (this.speakspeak == "") {
        this.$message.warning({ message: "请输入文字" });
        return;
      }
      this.$axios
        .post("/Blog/AddBlogSpeakSpeak", {
          blogSpeakSpeak: this.speakspeak,
          blogSpeakUser: this.id,
          blogSpeakId: this.blogSpeakId,
        })
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.$message.success({ message: "评论成功" });
              this.visible2 = false;
              this.speakspeak = "";
              this.GetBlogSpeak();
              this.GetBlogUserInfo();
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中" });
          }
        );
    },
    //监听窗口位置
    ListenBackTop() {
      window.addEventListener("scroll", () => {
        let scrollTop = document.documentElement.scrollTop;
        if (scrollTop >= 1000) {
          this.scrollTop = true;
        } else {
          this.scrollTop = false;
        }
      });
    },
    //回到顶部和底部
    backTopOne() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    backTopTwo() {
      window.scrollTo({
        top: document.body.scrollHeight,
        behavior: "smooth",
      });
    },
    //接收动态
    Listen() {
      this.$message.success({ message: "已为您开启此条博客接收信息" });
    },
    //翻译获取数据
    currentChange(e) {
      this.skip = e;
      this.GetBlogSpeak();
    },
    //关闭评论框
    beforeClose() {
      this.visible = false;
    },
    beforeClose2() {
      this.visible2 = false;
    },
    //获取页面信息及渲染信息
    GetBlogWithUserMessage() {
      this.title = this.$route.query.title;
      this.introduce = this.$route.query.introduce;
      this.classify = this.$route.query.classify;
      this.blogTime = this.$route.query.time;
      this.love = this.$route.query.love;
      this.uuid = this.$route.query.detailId;
      this.id = localStorage.getItem("id");
    },
    //删除文章评论内容
    DeleteBlogSpeak(e) {
      this.$axios.get("/Blog/DeleteBlogSpeak?id=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.GetBlogSpeak();
            this.GetBlogUserInfo();
          } else {
            this.$message.error({ message: "删除失败" });
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中" });
        }
      );
    },
    //获取文章评论内容
    GetBlogSpeak() {
      this.$axios
        .get(`/Blog/GetBlogSpeak?skip=${this.skip}&id=${this.uuid}`)
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.speakList = res.data.data;
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中" });
          }
        );
    },
    //用户发表文章评论
    AddBlogSpeak() {
      if (this.blogSpeak == "") {
        this.$message.warning({ message: "输入不能为空" });
        return;
      }
      this.$axios
        .post("/Blog/AddBlogSpeak", {
          blogId: this.uuid,
          blogSpeak: this.blogSpeak,
          blogUserId: this.id,
        })
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.blogSpeak = "";
              this.$message.success({ message: "发布成功" });
              this.GetBlogSpeak();
              this.GetBlogUserInfo();
              this.visible = false;
            } else {
              this.$message.error({ message: "发布失败" });
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中" });
          }
        );
    },
    //获取发表文章的人的详情
    GetBlogUserInfo() {
      this.$axios.get("/Blog/GetBlogUserWithBlog?uuid=" + this.uuid).then(
        (res) => {
          this.userInfo = res.data.data;
          this.total = +this.userInfo[5];
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //获取文章内容
    GetBlogDetail() {
      this.$axios.get("/Blog/BlogDetail?uuid=" + this.uuid).then(
        (res) => {
          if (res.data.code == "1") {
            axios
              .get(
                "http://8.130.43.227/blog/"+res.data.data.content
              )
              .then((response) => {
            document.getElementById("detail").innerHTML = response.data;
              })
              .catch((error) => {
                console.error(error);
              });
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中" });
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  display: flex;
  justify-content: space-between;
  #detail {
    cursor: auto;
  }
  .left {
    width: 70%;
    margin-left: 4%;
  }
  .right {
    margin-right: 4%;
    width: 19%;
    margin-top: 1%;
  }
  .abc {
    position: fixed;
    margin-left: 80%;
    height: 100vh;
    overflow: auto;
    width: 19%;
    .title {
      padding: 2%;
      border-bottom: 1px dashed black;
    }
    .content {
      overflow: auto;
      padding: 2%;
      border-bottom: 1px dashed black;
      .username {
        display: flex;
        justify-content: space-between;
      }
      .speak {
        padding: 2%;
      }
      .time {
        font-size: 10px;
        opacity: 0.5;
        padding-left: 2%;
      }
    }
    .footer {
      margin-right: 1%;
      position: fixed;
      bottom: 0px;
    }
    &::-webkit-scrollbar {
      display: none;
    }
  }
}
.backTopOne {
  position: fixed;
  bottom: 15%;
  right: 5vw;
}
.backTopTwo {
  position: fixed;
  bottom: 5%;
  right: 5vw;
}
</style>