<template>
  <div>
    <el-dialog
      title="添加留言"
      :visible.sync="visible"
      :before-close="beforeClose"
    >
      <div class="textarea">
        <el-input
          type="textarea"
          placeholder="请输入留言内容"
          v-model="LeaveWords"
          rows="10"
          @input="sensitiveWord()"
        ></el-input>
        <span>{{ LeaveWords.length }}字 </span> 
          
      </div>
      <div
        class="button"
        style="display: flex; justify-content: space-around; margin-top: 2vh"
      >
        <el-button @click="Reset()">重置</el-button>
        <el-button @click="AddApiLeaveWord()" type="primary">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      LeaveWords: "",
      visible: true,
    };
  },
  mounted() {},
  methods: {
    //回退路由
    beforeClose() {
      this.$router.back();
    },
    //发布内容
    AddApiLeaveWord() {
      if (this.LeaveWords == "") return;
      const id = localStorage.getItem("id");
      let time = new Date().toLocaleString();
      this.$axios
        .post(`/LeaveWords/AddLeaveWords`, {
          id: id,
          leavewords: this.LeaveWords,
          time: time,
        })
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.$notify.success({ message: "添加成功", title: "消息：" })
              this.$bus.$emit("showAddLeaveWord")
              this.beforeClose()
            } else {
              this.$message.error({message:"添加失败"})
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({message:"系统维护中..."})
          }
        );
    },
    //敏感词过滤
    sensitiveWord() {
      const reg =
        /他妈|你妈|操|傻逼|鸡巴|JB|脑残|恶心|废物|有病|无耻|废物|妈的|智障/gi;
      if (reg.test(this.LeaveWords)) {
        this.LeaveWords = this.LeaveWords.replace(reg, "*");
      }
    },
    //重置内容并获取焦点
    Reset() {
      this.LeaveWords = "";
      this.$refs.AddLeaveWords.focus();
    },
  },
};
</script>

<style lang="less" scoped>
span {
  float: right;
  font-size: 5px;
}
textarea::-webkit-scrollbar {
  display: none;
}
</style>