<template>
  <div>
      <el-empty description="为了您的良好体验 请使用电脑登录"></el-empty>
    <div style="display: flex; justify-content: center">
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
    this.IfWindowMobile();
  },
  methods: {
    IfWindowMobile() {
      var userAgent = navigator.userAgent;
      var isMobile =
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          userAgent
        );
      if (!isMobile) {
        this.$router.replace("/");
      }
    },
  },
};
</script>

