<template>
  <div>
    <div class="box">
      <div class="left">
        <div class="userList" v-if="helpUserList.length != 0">
          <div
            class="item"
            style="margin-top: 2%"
            v-for="item in helpUserList"
            :key="item.id"
          >
            <div style="display: flex; justify-content: space-between">
              <div class="avatar">
                <el-avatar :src="imgUrl" shape="square"></el-avatar>
                <div
                  class="online"
                  style=""
                  :class="item.status == 0 ? 'active' : ''"
                ></div>
              </div>

              <div class="college" style="margin-top: 2%; font-size: 1.5vw">
                {{ item.college }}
              </div>
            </div>
            <div
              style="
                display: flex;
                justify-content: space-between;
                border-bottom: 1px dashed black;
                padding-bottom: 2%;
              "
            >
              <div
                class="username"
                style="margin-top: 2%; font-size: 15px; font-size: 1vw"
              >
                {{ item.major }} {{ item.username }}
              </div>
              <el-button
                size="mini"
                style="margin-top: 2%"
                @click="UserChat(item.id)"
                >发起私聊</el-button
              >
            </div>
          </div>
        </div>
        <div v-else>
          <el-empty description="暂无社员"></el-empty>
        </div>
      </div>
      <div class="right" id="rightHeight">
        <div v-if="bugHelpList.length != 0">
          <div
            class="discussion"
            @click="ToBugHelpDetail(item.id)"
            v-for="item in bugHelpList"
            :key="item.id"
          >
            <div class="rightDiscussion">
              <div style="display: flex">
                <div class="userImg">
                  <el-avatar :src="imgUrl" style="margin: 22%"></el-avatar>
                </div>
                <div class="title">{{ item.title }}</div>
              </div>
              <div class="content">
                {{ item.content }}
              </div>
              <div class="footer">
                <div class="look">
                  <i class="el-icon-view"></i>
                  <span style="font-size: 10px; margin-left: 5%">{{
                    item.look
                  }}</span>
                </div>
                <div class="speak">
                  <i class="el-icon-chat-round"></i>
                  <span style="font-size: 10px; margin-left: 5%">{{
                    item.speakCount
                  }}</span>
                </div>
                <div class="close" v-if="id == item.userId">
                  <el-button @click.stop="DeleteBugHelp(item.id)" size="mini"
                    >删除内容</el-button
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
        <div v-else>
          <el-empty description="没有讨论内容"></el-empty>
        </div>
      </div>

      <div class="setup1">
        <el-dropdown @command="commandDropdown">
          <el-button icon="el-icon-setting" circle plain></el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="closeWebSocket"
              >关闭私聊</el-dropdown-item
            >
            <el-dropdown-item command="openWebSocket"
              >打开私聊</el-dropdown-item
            >
            <el-dropdown-item command="closeWebSocketMessage"
              >拒收留言</el-dropdown-item
            >
            <el-dropdown-item command="openWebSocketMessage"
              >接收留言</el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="setup2">
        <el-button
          @click="AddBugHelp()"
          circle
          plain
          icon="el-icon-circle-plus-outline"
        ></el-button>
      </div>
      <div class="setup3">
        <el-dropdown @command="changeUserHelp">
          <el-button
            icon="
          el-icon-arrow-down"
            circle
          ></el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="2015">2015</el-dropdown-item>
            <el-dropdown-item command="2016">2016</el-dropdown-item>
            <el-dropdown-item command="2017">2017</el-dropdown-item>
            <el-dropdown-item command="2018">2018</el-dropdown-item>
            <el-dropdown-item command="2019">2019</el-dropdown-item>
            <el-dropdown-item command="2020">2020</el-dropdown-item>
            <el-dropdown-item command="2021">2021</el-dropdown-item>
            <el-dropdown-item command="2022">2022</el-dropdown-item>
            <el-dropdown-item command="2023">2023</el-dropdown-item>
            <el-dropdown-item command="2024" disabled>2024</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="setup4">
        <el-tooltip content="反馈建议" placement="top">
          <el-button
            circle
            type="danger"
            @click="NeedHelpChatBug()"
            plain
            icon="el-icon-service"
          ></el-button>
        </el-tooltip>
      </div>
      <div class="setup5" v-if="showToTop">
        <el-button
          circle
          type="primary"
          plain
          icon="el-icon-arrow-up"
          @click="ContainerHeigth()"
        ></el-button>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      imgUrl: require("../assets/Index/2020112022562230967.jpg"),
      year: 2022,
      helpUserList: [],
      loading: null,
      showToTop: false,
      skip: 1,
      bugHelpList: [],
      bugHelpListAdd: true,
    };
  },
  mounted() {
    this.$bus.$on("closeLodingChatBugHelp", () => {
      this.loading.close();
    });
    this.$bus.$on("AddBugHelpBus", () => {
      this.skip = 1;
      this.GetBugHelpAll();
    });
    this.id = localStorage.getItem("id");
    this.BugHelpUser();
    this.ContainerHeigth();
    this.GetBugHelpAll();
  },
  methods: {
    //删除问题
    DeleteBugHelp(e) {
      this.$axios.get("/BugHelp/DeleteBugHelp?id=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.skip = 1;
            this.GetBugHelpAll();
          } else {
            this.$message.error({ message: "删除失败" });
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //跳转问题详情页面
    ToBugHelpDetail(e) {
      this.$axios.get("/BugHelp/AddBugHelpLook?id=" + e).then();
      this.$router.push({
        path: "/BugHelpDetail",
        query: {
          BugHelpId: e,
        },
      });
    },
    //获取所有的问题
    GetBugHelpAll() {
      this.$axios.get("/BugHelp/GetBugHelpAll?skip=" + this.skip).then(
        (res) => {
          if (res.data.code == "1") {
            this.bugHelpList = res.data.data.records;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //添加内容
    AddBugHelp() {
      this.$router.push("/ChatBugHelp/AddBugHelp");
    },
    //获取元素盒子高度位置
    ContainerHeigth() {
      let container = document.getElementById("rightHeight");
      container.addEventListener("scroll", () => {
        if (
          container.scrollTop + container.clientHeight >=
          container.scrollHeight
        ) {
          if (this.bugHelpList) {
            this.skip++;
            this.changeSkip();
          }
        }
        if (container.scrollTop > 1000) {
          this.showToTop = true;
        } else {
          this.showToTop = false;
        }
      });
      if (container) {
        container.scrollTop = 0;
      }
    },
    //改变分页重新获取
    changeSkip() {
      if (this.bugHelpListAdd) {
        this.$axios.get("/BugHelp/GetBugHelpAll?skip=" + this.skip).then(
          (res) => {
            let oldList = this.bugHelpList;
            if (res.data.code == "1") {
              this.bugHelpList = [
                ...this.bugHelpList,
                ...res.data.data.records,
              ];
              if (oldList.length == this.bugHelpList.length) {
                this.bugHelpListAdd = false;
                this.skip = 1;
              }
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        );
      }
    },
    //关闭私聊功能
    commandDropdown(command) {
      if (command == "closeWebSocket") {
        if (localStorage.getItem("websocket") == null) {
          this.$websocket.close();
          localStorage.setItem("websocket", "close");
          this.$message.success({ message: "关闭成功" });
        } else {
          this.$message.info({ message: "当前为关闭状态" });
        }
      } else if (command == "openWebSocket") {
        if (localStorage.getItem("websocket") != null) {
          localStorage.removeItem("websocket");
          this.$bus.$emit("openWebSocket");
          this.$message.success({ message: "打开成功" });
        } else {
          this.$message.info({ message: "当前为打开状态" });
        }
      } else if (command == "closeWebSocketMessage") {
        if (localStorage.getItem("websocketMessage") == null) {
          this.$axios
            .get("/Ws/SetMessageClose?id=" + localStorage.getItem("id"))
            .then((res) => {
              console.log(res);
              localStorage.setItem("websocketMessage", "close");
              this.$message.success({ message: "关闭接收成功" });
            });
        } else {
          this.$message.info({ message: "当前为关闭状态" });
        }
      } else if (command == "openWebSocketMessage") {
        if (localStorage.getItem("websocketMessage") != null) {
          this.$axios
            .get("/Ws/SetMessageOpen?id=" + localStorage.getItem("id"))
            .then(() => {
              localStorage.removeItem("websocketMessage");
              this.$message.success({ message: "打开接收成功" });
            });
        } else {
          this.$message.info({ message: "当前为打开状态" });
        }
      }
    },
    //改变求助人物
    changeUserHelp(command) {
      this.year = command;
      this.BugHelpUser();
    },
    //获取求助用户的信息
    BugHelpUser() {
      this.$axios.get(`/User/BugHelpUser?id=${this.id}&year=${this.year}`).then(
        (res) => {
          if (res.data.code == "1") {
            this.helpUserList = res.data.data;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error("系统维护中...");
        }
      );
    },
    //发起私聊
    UserChat(e) {
      if (localStorage.getItem("websocket") != null) {
        this.$message.warning({ message: "请先开启私聊功能" });
        return;
      }
      this.$axios.get("/Ws/chatId?chatId=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            const message = {
              to: e,
              content: "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj申请私聊",
            };
            this.$websocket.send(JSON.stringify(message));
            this.loading = this.$loading({
              text: "正在等待对方响应",
              spinner: "el-icon-loading",
              background: "rgba(0, 0, 0, 0.7)",
            });
          } else {
            this.$message.warning({ message: "对方不在线" });
            setTimeout(() => {
              this.SaveMessage(e);
            }, 500);
          }
        },
        (err) => {
          console.log(err);
          this.$message.error("系统维护中...");
        }
      );
    },
    //当对方不在线时给对方发起存储私聊功能，等对方上线推送给对方
    SaveMessage(e) {
      this.$axios.get("/Ws/IfWebSocketCan?id=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$confirm("对方当前不在线，是否发起留言", "软件协会", {
              type: "warning",
            })
              .then(() => {
                this.$router.push({
                  path: "/ChatBugHelp/AddSaveMessage",
                  query: {
                    to_userid: e,
                  },
                });
              })
              .catch(() => {});
          } else {
            this.$message.error({ message: "对方拒收留言" });
          }
        },
        (err) => {
          console.log(err);
          this.$message.error("系统维护中...");
        }
      );
    },
    //帮助建议
    NeedHelpChatBug() {
      const HelpLoading = this.$loading({ text: "正在为您对接..." });
      setTimeout(() => {
        HelpLoading.close();
        this.$message.error({
          message: "对方当前不在线，急需请联系社团负责人",
        });
      }, 2000);
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  width: 99%;
  height: 88vh;
  position: fixed;
  display: flex;
  background-color: #f7f8fa;
  .left {
    border: 1px solid #f2f2f2;
    width: 20%;
    height: 100%;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    .userList {
      padding: 3%;
      .online {
        background-color: #41b883;
        width: 1vh;
        height: 1vh;
        border-radius: 50%;
        margin-left: 100%;
        &.active {
          background-color: gray;
        }
      }
    }
  }

  .right {
    width: 70%;
    margin-left: 2%;
    overflow: auto;
    scroll-behavior: smooth;
    .discussion {
      width: 90%;
      margin-left: 5%;
      margin-top: 2%;
      height: 30vh;
      background-color: #ffffff;
      border-radius: 20px;
      .rightDiscussion {
        width: 100%;
        .userImg {
          img {
            width: 10%;
          }
        }
        .title {
          width: 100%;
          margin: 4%;
          overflow: hidden;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
        }
        .content {
          width: 90%;
          margin-left: 5%;
          text-indent: 2em;
          overflow: hidden;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
        }
        .footer {
          margin: 2%;
          display: flex;
          opacity: 0.5;
          .look {
            width: 20%;
            margin-left: 5%;
          }
          .speak {
            width: 20%;
          }
        }
      }
    }
    &::-webkit-scrollbar {
      display: none;
    }
  }
  .setup1 {
    position: fixed;
    right: 2%;
    top: 12%;
  }
  .setup2 {
    position: fixed;
    right: 2%;
    top: 22%;
  }
  .setup3 {
    position: fixed;
    right: 2%;
    top: 33%;
  }
  .setup4 {
    position: fixed;
    right: 2%;
    bottom: 15%;
  }
  .setup5 {
    position: fixed;
    right: 2%;
    bottom: 5%;
  }
}
</style>