<template>
  <div>
    <div class="tip" v-if="showtext">
      <WindowTip :tip="tip" />
    </div>
    <div class="box">
      <div
        class="item"
        @click="ToBlogDetail(item.uuid)"
        v-for="item in blog"
        :key="item.uuid"
      >
        <div class="img">
          <img :src="'http://8.130.43.227/images/'+item.img" alt="图片加载失败" v-if="item.img != ''" />
          <img src="../assets/image/backimg1.jpg" alt="图片加载失败" v-else />
        </div>

        <div class="title">{{ item.title }}</div>
        <div class="introduce">
          {{ item.introduce }}
        </div>
        <div class="classify">分类：{{ item.classify }}</div>
        <div class="love">❤：{{ item.love }}</div>
        <div class="delete">
          <el-popconfirm title="是否删除？" @confirm="DeleteMyBlog(item.uuid)">
            <button slot="reference" @click.stop="None()">删除</button>
          </el-popconfirm>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import WindowTip from "@/components/WindowTip.vue";
export default {
  components: { WindowTip },

  data() {
    return {
      blog: [],
      showtext: false,
      tip: "",
    };
  },
  methods: {
    None(){},
    //跳转详情页
    ToBlogDetail(e) {
      this.$router.push({
        path: "/BlogDetail",
        query: {
          detailId: e,
        },
      });
    },
    //处理用户删除自己的博客
    DeleteMyBlog(e) {
      this.$axios.get("/Blog/DeleteMyBlog?uuid=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.tip = "删除成功";
            this.showtext = true;
            this.blog = [];
            this.GetMyblogInfo();
            setTimeout(() => {
              this.showtext = false;
            }, 4000);
          }
        },
        (err) => {
          console.log(err);
          alert("系统维护中...");
        }
      );
    },
    //处理管理员获取所有的博客
    GetMyblogInfo() {
      this.$axios.get("/Blog/GetAdminAllBlog").then(
        (res) => {
          if (res.data.code == "1") {
            this.blog = res.data.data
          } else {
            this.$message.info({message:"没有内容"})
          }
        },
        (err) => {
          console.log(err);
          alert("系统维护中...");
        }
      );
    },
  },
  mounted() {
    this.GetMyblogInfo();
  },
};
</script>
  
  <style lang="less" scoped>
.box {
  width: 85vw;
  height: 89vh;
  overflow: auto;
  margin-left: 15vw;
  margin-top: -89vh;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  overflow: auto;

  .item {
    width: 30%;
    height: auto;
    margin: 1vh;
    border: 1px solid black;
    border-radius: 10px;
    img {
      width: 90%;
      margin: 5%;
      height: 20vh;
    }
    .title {
      font-size: 2vw;
      margin: 1vh;
    }
    .introduce {
      margin: 1vh;
      font-size: 1vw;
    }
    .classify {
      margin: 1vh;
      font-size: 1vw;
    }
    .love {
      margin: 1vh;
      font-size: 1vw;
    }
    button {
      float: right;
      color: white;
      background-color: blue;
      border: none;
      padding: 5px 10px;
      border-radius: 5px;
      margin-right: 1vw;
      margin-left: 0.5vw;
      cursor: pointer;
      margin-bottom: 1vh;
      transition: background-color 0.3s ease;

      &:hover {
        background-color: darkblue;
      }
    }
    &:hover {
      border: 1px solid blue;
    }
  }
}
.box::-webkit-scrollbar {
  display: none;
}
</style>