<template>
  <div>
    <el-dialog :visible="visible" title="发起留言" :before-close="beforeClose">
      <el-form :model="form" :rules="rules" ref="AddSaveMessageRef">
        <el-form-item label="留言内容：" prop="message">
          <el-input
            @input="sensitiveWord"
            v-model="form.message"
            type="textarea"
            rows="10"
            show-word-limit
            maxlength="500"
          ></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: flex-end; margin-top: 2%">
        <el-button size="mini" type="" @click="beforeClose">取消</el-button>
        <el-button size="mini" type="primary" @click="validate()"
          >确定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: true,
      form: {
        userid: localStorage.getItem("id"),
        message: "",
        toUserid: "",
      },
      rules: {
        message: [{ required: true, message: "请输入留言内容" }],
      },
    };
  },
  mounted() {
    this.form.toUserid = this.$route.query.to_userid;
  },
  methods: {
    //规则校验是否涉及敏感词
    sensitiveWord() {
      const reg =
        /微信号|QQ号|vx|手机号|额|钱|金|money|元|圆|yuan|园|weixin|wx|微信|金钱|qq|QQ|V|WEIXIN|WX|weixinhao|手机/gi;
      if (reg.test(this.form.message)) {
        this.form.message = this.form.message.replace(reg, "*");
      }
    },
    //规则校验
    validate() {
      this.$refs.AddSaveMessageRef.validate((e) => {
        if (e) {
          this.$axios.post("/Ws/SaveMessage", this.form).then(
            (res) => {
              if (res.data.code == "1") {
                this.$message.success({ message: "已向对方发送，待对方上线" });
                this.beforeClose();
              } else {
                this.$message.error({ message: "发送失败" });
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
        }
      });
    },
    //路由回退
    beforeClose() {
      this.$router.back();
    },
  },
};
</script>

