<template>
  <div class="all">
    <quill-editor
      style="width: 90%"
      class="editor"
      v-model="content"
    ></quill-editor>
    <el-button
      type="primary"
      @click="AddBlogNew()"
      style="position: fixed; right: 0; top: 0; margin: 1%"
      >提交</el-button
    >
    <el-button
      type="success"
      @click="ToImage()"
      style="position: fixed; right: 0; top: 20%; margin: 1%"
      >代码着色</el-button
    >
    <p style="position: fixed; right: 0; top: 40%; margin: 1%;font-size:10px;color:red">大小100M</p>

    <router-view></router-view>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";

export default {
  components: {
    quillEditor,
  },
  data() {
    return {
      content: "",
    };
  },
  methods: {
    //代码着色
    ToImage() {
      window.open("https://carbon.now.sh/");
    },
    //添加图片替换url
    insertImage() {
      const range = this.$refs.myQuillEditor.quill.getSelection();
      const url =
        "http://8.130.43.227:5551/images/242713c1-84a9-487a-bc9c-0915055b61a9_backone.jpg"; // 替换为你要插入的图片的URL

      if (range) {
        this.$refs.myQuillEditor.quill.insertEmbed(range.index, "image", url);
      }
    },
    //提交
    AddBlogNew() {
      if (this.content == "") {
        this.$message.warning({ message: "内容不得为空" });
        return;
      }
      // 将内容转换为Blob对象
      const blob = new Blob([this.content], { type: "text/plain" });

      // 获取Blob的大小（以字节为单位）
      const contentSizeInBytes = blob.size;

      // 将字节转换为MB
      const contentSizeInMB = contentSizeInBytes / (1024 * 1024);

      if (contentSizeInMB >= 100) {
        this.$message.warning({ message: "内容大小不得超过100MB" });
        return;
      }

      this.$router.push({
        path: "/BlogNew/AddBlogNew",
        query: {
          content: this.content,
        },
      });
    },
  },
  mounted() {},
};
</script>

<style lang="less" scoped>
.editor {
  position: fixed;
  height: 90vh;
  margin-bottom: 10vh;
}
</style>
