<template>
  <div>
    <el-dialog
      width="80vw"
      :visible.sync="visible"
      title="发布讨论"
      :before-close="beforeClose"
    >
      <el-form label-width="auto">
        <el-form-item label="标题：">
          <el-input clearable v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="讨论内容：">
          <el-input
            type="textarea"
            maxlength="4000"
            rows="10"
            show-word-limit
            v-model="form.content"
          ></el-input>
        </el-form-item>
      </el-form>
      <div style="display:flex;justify-content:flex-end">
        <el-button style="background-color:#1e1e1e;color:white" @click="AddBugHelp()">发布</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: true,
      form:{
        title:"",
        content:"",
        userId:""
      }
    };
  },
  mounted() {
    this.form.userId = localStorage.getItem("id")
  },
  methods: {
    //发表讨论内容
    AddBugHelp(){
        this.$axios.post("/BugHelp/AddBugHelp",this.form).then(
            res =>{
                if(res.data.code == "1"){
                    this.$message.success({message:"发布成功"})
                    this.beforeClose()
                    this.$bus.$emit("AddBugHelpBus")
                }else{
                    this.$message.error({message:"发布失败"})
                }
            }
        )
    },
    //回退路由
    beforeClose() {
      this.$router.back();
    },
  },
};
</script>

