<template>
  <div>
    <div class="box">
      <div v-if="content.length != 0">
        <div class="bottom" v-for="item in content" :key="item.uuid">
          <div class="major">{{ item.major }}</div>
          <div class="username">{{ item.username }}</div>
          <div class="year">{{ item.year }}级</div>
          <div class="time">{{ item.time }}</div>
          <div class="time">
            <span style="color: red">❤</span>：{{ item.love }}
          </div>
          <div class="leavewords">留言内容：{{ item.leaveword }}</div>
          <div class="button">
            <el-popconfirm @confirm="clickDeleteLeaveword(item.uuid)" title="是否删除？">
              <button slot="reference">删除</button>
            </el-popconfirm>
          </div>
        </div>
      </div>
      <div v-else><el-empty description="没有内容"></el-empty></div>
    </div>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      id: "",
      content: [],
    };
  },
  mounted() {
    this.id = localStorage.getItem("id");
    this.GetUserInfoMyLeaveWords();
  },
  methods: {
    //删除此条留言
    clickDeleteLeaveword(e) {
      this.$axios.get("/LeaveWords/clickDeleteLeaveword?id=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.GetUserInfoMyLeaveWords();
          } else {
            this.$message.error({ message: "删除失败" });
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //获取当前用户的留言
    GetUserInfoMyLeaveWords() {
      this.$axios.get("/LeaveWords/GetUserInfoMyLeaveWords?id=" + this.id).then(
        (res) => {
          if (res.data.code == "1") {
            if (res.data.data.length != 0) {
              this.content = res.data.data;
              this.content.sort((a, b) => new Date(b.time) - new Date(a.time));
            }
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>
  
<style lang="less" scoped>
.box {
  overflow: auto;
  height: 89vh;
  .bottom {
    margin-top: 1vh;
    overflow: auto;
    width: 80vw;
    height: auto;
    border-radius: 10px;
    border: 1px solid #000;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: border-color 0.3s ease;

    &:hover {
      border-color: blue;
    }

    .major {
      font-size: 1.5vw;
      font-weight: bold;
      background-image: linear-gradient(
        to top,
        #dbdcd7 0%,
        #dddcd7 24%,
        #e2c9cc 30%,
        #e7627d 46%,
        #b8235a 59%,
        #801357 71%,
        #3d1635 84%,
        #1c1a27 100%
      );
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text;
    }

    .username {
      font-size: 1vw;
      margin-top: 2vh;
    }

    .year {
      font-size: 0.9vw;
      color: gray;
      margin-top: 2vh;
    }

    .time {
      font-size: 0.9vw;
      color: gray;
      margin-top: 2vh;
    }
    .leavewords {
      font-size: 1vw;
      margin-left: 13vw;
      width: 60vw;
      overflow: auto;
      word-wrap: break-word;
      height: 20vh;
      margin-top: -18vh;
      textarea {
        width: 50vw;
        overflow: auto;
        border: none;
        outline: none;
        height: 15vh;
      }
      textarea::-webkit-scrollbar {
        display: none;
      }
    }
    .leavewords::-webkit-scrollbar {
      display: none;
    }

    .button {
      display: flex;
      justify-content: flex-end;
      align-items: center;

      button {
        background-color: blue;
        color: white;
        border: none;
        padding: 5px 10px;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: darkblue;
        }
      }
    }
  }
}
.box::-webkit-scrollbar {
  display: none;
}
.bottom::-webkit-scrollbar {
  display: none;
}
</style>