<template>
  <div>
    <div class="box">
      <div v-if="share.length != 0">
        <div class="item" v-for="item in share" :key="item.uuid">
          <div class="content">
            网址：<span
              style="cursor: pointer"
              @click="OnopenNewHref(item.content)"
              >{{ item.content }}</span
            >
          </div>
          <div class="title">详情：{{ item.title }}</div>
          <div class="love">
            <span style="color: red">❤</span>：{{ item.love }}
          </div>
          <div class="delete">
            <el-popconfirm title="是否删除？" @confirm="DeleteShareUrl(item.uuid)">
              <button slot="reference">删除</button>
            </el-popconfirm>
          </div>
        </div>
      </div>
      <div v-else><el-empty description="没有内容"></el-empty></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      share: [],
    };
  },
  mounted() {
    this.GetMyUserShareUrl();
  },
  methods: {
    //打开新网站
    OnopenNewHref(e) {
      window.open(e);
    },
    //处理用户删除网址
    DeleteShareUrl(e) {
      this.$axios.get("/ShareUrl/DeleteShareUrl?uuid=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.GetMyUserShareUrl();
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //获取用户分享的网址
    GetMyUserShareUrl() {
      const id = localStorage.getItem("id");
      this.$axios.get("/ShareUrl/GetMyUserShareUrl?id=" + id).then(
        (res) => {
          if (res.data.code == "1") {
            this.share = res.data.data;
            this.share.sort((a, b) => b.sort - a.sort);
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  overflow: auto;
  height: 90vh;
  .item {
    border-bottom: 1px dashed black;
    margin: 1vw;
    margin-bottom: 2vh;

    .title {
      margin: 1vw;
    }
    .content {
      margin: 1vw;
    }
    button {
      background-color: blue;
      color: white;
      border: none;
      padding: 5px 10px;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s ease;
      margin-bottom: 2vh;
      float: right;
      &:hover {
        background-color: darkblue;
      }
    }
    .love {
      color: gray;
      margin: 1vw;
    }
  }
}
</style>