<template>
  <div>
    <div class="box">
      <div v-if="share.length != 0">
        <div class="item" v-for="item in share" :key="item.uuid">
          <div class="content">
            网址：<span
              style="cursor: pointer"
              @click="OnopenNewHref(item.content)"
              >{{ item.content }}</span
            >
          </div>
          <div class="title">详情：{{ item.title }}</div>
          <div class="love">
            <span style="color: red">❤</span>：{{ item.love }}
          </div>
          <div class="delete">
            <el-popconfirm
              title="是否删除？"
              @confirm="DeleteShareUrlCollector(item.uuid)"
            >
              <button slot="reference">删除</button>
            </el-popconfirm>
          </div>
        </div>
      </div>
      <div v-else><el-empty description="没有内容"></el-empty></div>
    </div>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      id: "",
      share: [],
    };
  },
  mounted() {
    this.id = localStorage.getItem("id");
    this.MyShareUrlCollector();
  },
  methods: {
    //获取用户所收藏的网址
    MyShareUrlCollector() {
      this.$axios.get("/ShareUrl/MyShareUrlCollector?userId=" + this.id).then(
        (res) => {
          if (res.data.code == "1") {
            this.share = res.data.data;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //打开新网站
    OnopenNewHref(e) {
      window.open(e);
    },
    //处理用户删除网址
    DeleteShareUrlCollector(e) {
        console.log(e)
        console.log(this.id)
      this.$axios.get(`/ShareUrl/DeleteShareUrlCollector?userId=${this.id}&shareUrlId=${e}`).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.MyShareUrlCollector();
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>
  
  <style lang="less" scoped>
.box {
  overflow: auto;
  height: 90vh;
  .item {
    border-bottom: 1px dashed black;
    margin: 1vw;
    margin-bottom: 2vh;

    .title {
      margin: 1vw;
    }
    .content {
      margin: 1vw;
    }
    button {
      background-color: blue;
      color: white;
      border: none;
      padding: 5px 10px;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s ease;
      margin-bottom: 2vh;
      float: right;
      &:hover {
        background-color: darkblue;
      }
    }
    .love {
      color: gray;
      margin: 1vw;
    }
  }
}
</style>