<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" title="提交">
      <el-form label-width="100px">
        <el-form-item label="请输入标题：">
          <el-input v-model="title" style="width: 80%"></el-input>
        </el-form-item>
        <el-form-item label="分类：">
          <el-select name="" v-model="blogClassify">
            <el-option value="JavaScript">JavaScript</el-option>
            <el-option value="Java">Java</el-option>
            <el-option value="Git">Git</el-option>
            <el-option value="MySQL">MySQL</el-option>
            <el-option value="Linux">Linux</el-option>
            <el-option value="攻防">攻防</el-option>
            <el-option value="安全">安全</el-option>
            <el-option value="其它">其它</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="请输入介绍：">
          <el-input
            type="textarea"
            v-model="introduce"
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="首页图片：">
          <label for="file">
            <div
              style="
                width: 100px;
                height: 100px;
                border: 1px dashed black;
                text-align: center;
                line-height: 100px;
              "
            >
              <i class="el-icon-picture-outline-round" v-if="img == ''"></i>
              <img
                style="width: 100px; height: 100px"
                src=""
                id="img"
                alt=""
                v-else
              />
            </div>
            <span style="color: red">注：每个文章内容大小不得小于100MB</span>
          </label>
          <input
            type="file"
            @change="changeInput()"
            ref="BlogFile"
            id="file"
            accept="image/*"
            style="display: none"
          />
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: space-around">
        <el-button @click="beforeClose()">取消</el-button>
        <el-button @click="Submit()" type="primary">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: true,
      title: "",
      introduce: "",
      img: "",
      content: "",
      blogClassify: "",
    };
  },
  mounted() {
    this.content = this.$route.query.content;
  },
  methods: {
    //64进制渲染图片
    changeInput() {
      const file = this.$refs.BlogFile.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        document.getElementById("img").src = e.target.result;
      };
      this.img = "1";
    },
    //回退路由
    beforeClose() {
      this.$router.back();
    },
    //提交按钮
    async Submit() {
      const id = localStorage.getItem("id");
      const time = Date.now();
      const file = this.$refs.BlogFile.files[0];
      const formdata = new FormData();
      if (
        id == "" ||
        file == null ||
        this.blogClassify == "" ||
        time == "" ||
        this.title == "" ||
        this.introduce == "" ||
        this.content == ""
      ) {
        this.$message.warning({ message: "所有字段为必填项" });
        return;
      }
      const loading = this.$loading({ text: "正在上传" });
      formdata.append("file", file);
      formdata.append("id", id);
      formdata.append("title", this.title);
      formdata.append("introduce", this.introduce);
      formdata.append("classify", this.blogClassify);
      formdata.append("content", this.content);
      formdata.append("time", time);
      this.$axios
        .post("/Blog/AddBlogNew", formdata)
        .then(
          (res) => {
            if (res.data.code == "1") {
              loading.close();
              this.$message.success({ message: "添加成功" });
              this.$router.replace("/AssociationIndex");
            } else if (res.data.code == "0") {
              this.$message.success({ message: "添加失败" });
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        )
        .catch((error) => {
          console.error("An error occurred:", error);
        });
    },
  },
};
</script>

