<template>
  <div>
    <div class="box">
      <div class="left">
        <el-card>
          <div class="top">
            <span style="opacity: 0.5">讨论 /</span> {{ BugHelpDetail.title }}
          </div>
          <div class="header">
            <el-avatar :src="imgUrl"></el-avatar>
            <span style="font-size: 10px; opacity: 0.5; margin-left: 4%"
              >{{ userDetail.college }} · {{ userDetail.major }} ·
              {{ userDetail.username }} · 发起于
              {{ BugHelpDetail.bugHelpTime }}
            </span>
          </div>
          <div class="content">
            <pre>{{ BugHelpDetail.content }}</pre>
          </div>
          <div class="footer" style="display: flex; justify-content: flex-end">
            <el-button
              icon="el-icon-edit"
              type="success"
              @click="ContentFocus()"
              >回复讨论</el-button
            >
            <el-button
              icon="el-icon-bell"
              style="
                border: 1px solid green;
                background-color: white;
                color: balck;
              "
              @click="listen()"
              >接收动态</el-button
            >
          </div>
        </el-card>
        <el-card style="margin-top: 2%">
          <div style="display: flex; justify-content: space-between">
            <span style="font-size: 10px; opacity: 0.5"
              >共 {{ BugHelpDetail.speakCount }} 个回复</span
            >
            <el-dropdown trigger="click">
              <span style="font-size: 10px; opacity: 0.5; cursor: pointer"
                >最热<i class="el-icon-sort"></i
              ></span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>最热</el-dropdown-item>
                <el-dropdown-item>最新</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-card>
        <el-card
          style="margin-top: 2%"
          v-for="item in bugSpeakList"
          :key="item.bugSpeak.id"
        >
          <div>
            <el-avatar :src="imgUrl"></el-avatar>
            <span style="font-size: 10px; opacity: 0.5; margin-left: 4%"
              >{{ item.user.college }} · {{ item.user.major }} ·
              {{ item.user.username }} · 发起于
              {{ item.bugSpeak.speakTime }}
            </span>
          </div>

          <div style="font-size: 13px; margin: 4%">
            <pre>{{ item.bugSpeak.content }}</pre>
          </div>
          <el-card
            v-if="item.bugSpeak.codeContent.length != 0"
            style="margin-top: 2%"
          >
            <pre id="hljsCode">{{ item.bugSpeak.codeContent }}</pre>
          </el-card>
          <div style="display: flex; justify-content: flex-end; margin-top: 2%">
            <el-button
              size="mini"
              v-if="id == item.user.id"
              @click="
                DeleteBugSpeak(item.bugSpeak.id, item.bugSpeak.bugDetailId)
              "
              >删除</el-button
            >
          </div>
        </el-card>
      </div>
      <div class="right">
        <el-card>
          <div
            style="
              display: flex;
              justify-content: space-between;
              font-size: 10px;
              opacity: 0.6;
            "
          >
            <div>浏览次数：</div>
            <div>
              {{ BugHelpDetail.look }}
            </div>
          </div>
          <div
            style="
              display: flex;
              justify-content: space-between;
              font-size: 10px;
              opacity: 0.6;
              margin-top: 10%;
            "
          >
            <div>参与人数：</div>
            <div>{{ BugHelpDetail.speakCount + 1 }}</div>
          </div>
          <div style="margin-top: 10%; opacity: 0.7">相关标签</div>
          <div style="margin-top: 5%">
            · <el-tag size="mini">数据结构与算法</el-tag>
          </div>
          <div style="margin-top: 5%">
            · <el-tag size="mini" type="success">问题求助</el-tag>
          </div>
        </el-card>
        <el-card style="margin-top: 10%; text-align: center">
          <el-tooltip content="积极讨论" placement="top">
            <span>软件协会</span>
          </el-tooltip>
        </el-card>
        <div class="edit">
          <el-input
            ref="ContentFocusRef"
            type="textarea"
            rows="8"
            placeholder="发表评论"
            show-word-limit
            maxlength="10000"
            v-model="content"
          ></el-input>
          <div
            style="
              display: flex;
              justify-content: space-between;
              margin-top: 5%;
            "
          >
            <el-button
              style="background-color: #1e1e1e; color: white"
              @click="visible = true"
              >添加代码块</el-button
            >
            <el-button type="success" @click="AddBugSpeak()"
              >发表评论</el-button
            >
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      :visible.sync="visible"
      :before-close="beforeClose"
      title="添加代码块"
    >
      <el-input
        type="textarea"
        v-model="codeContent"
        rows="10"
        placeholder="请输入代码块"
        show-word-limit
        maxlength="15000"
      ></el-input>
      <div
        style="display: flex; justify-content: space-around; margin-top: 10%"
      >
        <el-button
          style="background-color: #7f7f7f; color: white"
          @click="AddCodeContent()"
          >粘贴</el-button
        >
        <el-button
          style="background-color: #ff7043; color: white"
          @click="codeContent = ''"
          >重置</el-button
        >
        <el-button
          style="background-color: #000000; color: white"
          @click="beforeClose"
          >确定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import hljs from "highlight.js";
// import "/node_modules/cli-highlight/node_modules/highlight.js/styles/atom-one-dark-reasonable.css";
import 'highlight.js/styles/atom-one-dark-reasonable.css';

export default {
  data() {
    return {
      id: "",
      BugHelpId: "",
      BugHelpDetail: {},
      userDetail: {},
      imgUrl: require("../assets/Index/2020112022562230967.jpg"),
      content: "",
      codeContent: "",
      visible: false,
      skip: 1,
      bugSpeakList: [],
    };
  },
  mounted() {
    this.id = localStorage.getItem("id");
    this.BugHelpId = this.$route.query.BugHelpId;
    this.GetBugHelpDetail();
    this.GetBugSpeakAll();
  },
  methods: {
    //回复评论消息动态
    ContentFocus() {
      this.$refs.ContentFocusRef.focus();
    },
    //接收动态
    listen() {
      this.$message.success({ message: "已为您开启WebSocket推送" });
    },
    //删除问题的评论
    DeleteBugSpeak(e1, e2) {
      this.$axios
        .get(`/BugHelp/DeleteBugSpeak?id=${e1}&bugDetailId=${e2}`)
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.$message.success({ message: "删除成功" });
              this.GetBugHelpDetail();
              this.GetBugSpeakAll();
            } else {
              this.$message.error({ message: "删除失败" });
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        );
    },
    //获取当前评论条数
    GetBugSpeakAll() {
      this.$axios
        .get(
          `/BugHelp/GetBugSpeakAll?skip=${this.skip}&bugDetailId=${this.BugHelpId}`
        )
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.bugSpeakList = res.data.data;
              this.$nextTick(() => {
                let code = document.getElementById("hljsCode");
                if (code) {
                  hljs.highlightElement(code);
                }
              });
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        );
    },
    //粘贴代码块
    AddCodeContent() {
      navigator.clipboard
        .readText()
        .then((text) => {
          this.codeContent = text;
        })
        .catch((error) => {
          this.$message.error({
            message: "浏览器不支持api粘贴代码块，请手动粘贴",
          });
          console.log(error);
        });
    },
    //改变添加代码块
    beforeClose() {
      this.visible = false;
    },
    //发表评论
    AddBugSpeak() {
      if (this.content == "") {
        this.$message.warning({ message: "请输入评论内容" });
        return;
      }
      this.$axios
        .post("/BugHelp/AddBugSpeak", {
          bugDetailId: this.BugHelpId,
          userId: this.id,
          content: this.content,
          codeContent: this.codeContent,
        })
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.$message.success({ message: "发表成功" });
              this.GetBugSpeakAll();
              this.GetBugHelpDetail();
              this.content = "";
              this.codeContent = "";
            } else {
              this.$message.error({ message: "发表失败" });
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        );
    },
    //获取当前条数的个人信息
    GetBugHelpDetail() {
      this.$axios.get("/BugHelp/BugHelpDetail?id=" + this.BugHelpId).then(
        (res) => {
          if (res.data.code == "1") {
            this.BugHelpDetail = res.data.data.bugHelp;
            this.userDetail = res.data.data.user;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  display: flex;
  width: 100%;
  .left {
    margin-left: 14%;
    width: 50%;
    .top {
      border-bottom: 1px dashed rgba(0, 0, 0, 0.32);
    }
    .header {
      margin-top: 2%;
    }
    .content {
      text-indent: 2em;
      margin: 2%;
      padding: 2%;
      line-height: 2;
    }
    pre {
      white-space: pre-wrap;
    }
  }
  .right {
    width: 20%;
    position: fixed;
    left: 66%;
    .edit {
      margin-top: 10%;
    }
  }
}
</style>