<template>
  <div>
    <el-dialog
      :visible.sync="visible"
      :before-close="beforeClose"
      title="修改信息"
    >
      <el-form
        ref="AdminRightUserEditRef"
        label-width="100px"
        :model="form"
        :rules="rules"
      >
        <el-form-item label="姓名：" prop="username">
          <el-input
            v-model="form.username"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="学院：" prop="college">
          <el-input
            v-model="form.college"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="专业：" prop="major">
          <el-input
            v-model="form.major"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="年级：" prop="year">
          <el-input v-model="form.year" clearable style="width: 80%"></el-input>
        </el-form-item>
        <el-form-item label="状态：" prop="status">
          <el-select v-model="form.status" clearable>
            <el-option value="在线"></el-option>
            <el-option value="未在线"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮箱：">
          <el-input
            v-model="form.email"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="电话：">
          <el-input
            v-model="form.phone"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="微信：">
          <el-input
            v-model="form.wechat"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: space-around">
        <el-button @click="beforeClose">取消</el-button>
        <el-button type="primary" @click="submit('AdminRightUserEditRef')"
          >提交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        id: "",
        username: "",
        status: "",
        college: "",
        major: "",
        year: "",
        email: "",
        phone: "",
        wechat: "",
      },
      rules: {
        username: [{ required: true, message: "必填项" }],
        status: [{ required: true, message: "必填项" }],
        college: [{ required: true, message: "必填项" }],
        major: [{ required: true, message: "必填项" }],
        year: [{ required: true, message: "必填项" }],
      },
      visible: true,
    };
  },
  mounted() {
    //获取用户详情页
    this.id = this.$route.query.id;
    this.$axios.get("/User/AdminSelectUserList?id=" + this.id).then(
      (res) => {
        if (res.data.code == "1") {
          this.form = res.data.data;
          if (this.form.status == "0") {
            this.form.status = "未在线";
          } else {
            this.form.status = "在线";
          }
        }
      },
      (err) => {
        this.$message.error({ message: "系统维护中..." });
        console.log(err);
      }
    );
  },
  methods: {
    //验证规则
    submit(formdata) {
      this.$refs[formdata].validate((e) => {
        if (e) {
          this.AdminChangeUserInfo();
        } else {
          this.$message.warning({ message: "请填写必填项" });
        }
      });
    },
    //回退路由
    beforeClose() {
      this.$router.back();
    },
    //提交管理员修改的信息
    AdminChangeUserInfo() {
      if (
        this.id == "" ||
        this.username == "" ||
        this.password == "" ||
        this.college == "" ||
        this.major == "" ||
        this.year == ""
      ) {
        this.$message.warning({ message: "不得为空" });
      }
      if (this.form.status == "未在线") {
        this.form.status = "0";
      } else {
        this.form.status = "1";
      }
      this.$axios.post("/User/AdminChangeUserInfo", this.form).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "修改成功" });
            this.beforeClose();
            this.$bus.$emit("AdminOff");
          } else {
            this.$message.error({ message: "删除失败" });
          }
        },
        (err) => {
          this.$message.error({ message: "系统维护中..." });
          console.log(err);
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
</style>