<template>
  <div>
    <div class="box">
      <div class="top">
        <select
          name=""
          @change="leavewordSelectChoose()"
          ref="leavewordsyear"
          id="rank"
        >
          <option value="2015">2015级</option>
          <option value="2016">2016级</option>
          <option value="2017">2017级</option>
          <option value="2018">2018级</option>
          <option value="2019">2019级</option>
          <option value="2020">2020级</option>
          <option value="2021">2021级</option>
          <option value="2022" selected>2022级</option>
          <option value="2023">2023级</option>
          <option value="2024">2024级</option>
          <option value="2024">2025级</option>
        </select>
        共{{ count }}条 本页共 {{ content.length }} 条
      </div>
      <div v-if="content.length != 0">
        <div class="bottom" v-for="item in content" :key="item.uuid">
          <div class="major">{{ item.major }}</div>
          <div class="username">{{ item.username }}</div>
          <div class="year">{{ item.year }}级</div>
          <div class="time">{{ item.time }}</div>
          <div class="leavewords">
            <span style="color: red">留言内容：</span>{{ item.leaveword }}
          </div>
          <div
            class="love"
            @click="loveAddClick(item.uuid, item.loveid)"
            id="love"
            :class="loveChange(item.loveid) ? 'lovechange' : ''"
          >
            ❤ {{ item.love }}
          </div>
          <div class="button">
            <el-popconfirm
              title="是否删除？"
              @confirm="clickDeleteLeaveword(item.uuid)"
            >
              <button slot="reference" v-if="id == item.id">删除</button>
            </el-popconfirm>
          </div>
        </div>
      </div>
      <div v-else>
        <el-empty description="没有内容"></el-empty>
      </div>
    </div>
    <button class="buttonadd" @click="AddLeaveWords()">+</button>
    <router-view></router-view>
  </div>
</template>
    
    <script>
export default {
  data() {
    return {
      id: "",
      count: 0,
      year: 2022,
      content: [],
      oldTime: "",
      addText: "+",
    };
  },
  mounted() {
    this.GetLeaveWordsCount();
    this.GetLeaveWords();
    this.id = localStorage.getItem("id");
    this.$bus.$on("showAddLeaveWord", () => {
      this.GetLeaveWordsCount();
      this.GetLeaveWords();
    });
  },
  methods: {
    //对用户点赞进行操作
    loveAddClick(a, b) {
      const time = Date.now();
      if (this.oldTime != "") {
        if (this.oldTime - time < 2000) {
          this.$message.warning({ message: "不要频繁操作" });
          return;
        }
      }
      const ifAdd = this.loveChange(b);
      if (ifAdd) {
        this.$axios
          .get(`/LeaveWords/DeleteLeaveWordsLoveid?uuid=${a}&id=${this.id}`)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.GetLeaveWords();
                this.oldTime = Date.now();
                this.$message.info({ message: "(╯°□°）╯︵ ┻━┻" });
                setTimeout(() => {
                  this.oldTime = "";
                }, 4000);
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
      } else {
        this.$axios
          .get(`/LeaveWords/AddLeaveWordsLoveid?uuid=${a}&id=${this.id}`)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.GetLeaveWords();
                this.oldTime = Date.now();
                this.$message.success({ message: "(¬‿¬)" });
                setTimeout(() => {
                  this.oldTime = "";
                }, 4000);
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
      }
    },
    //判断是否点赞高亮
    loveChange(e) {
      const reg = new RegExp(this.id, "g");
      if (reg.test(e)) {
        return true;
      } else {
        return false;
      }
    },
    //添加留言内容
    AddLeaveWords() {
      this.$router.push("/IndexBottomLeaveWords/AddLeaveWord");
    },
    //选择不同的级别
    leavewordSelectChoose() {
      this.year = this.$refs.leavewordsyear.value;
      this.GetLeaveWords();
    },
    //删除此条留言
    clickDeleteLeaveword(e) {
      this.$axios.get("/LeaveWords/clickDeleteLeaveword?id=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.GetLeaveWordsCount();
            this.GetLeaveWords();
          } else {
            this.$message.error({ message: "删除失败" });
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //获取当前级别用户内容
    GetLeaveWords() {
      this.$axios.get("/LeaveWords/GetLeaveWords?year=" + this.year).then(
        (res) => {
          if (res.data.code == "1") {
            this.content = res.data.data;
            this.content.sort((a, b) => new Date(b.time) - new Date(a.time));
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //获取全部用户条数
    GetLeaveWordsCount() {
      this.$axios.get("/LeaveWords/GetLeaveWordsCount").then(
        (res) => {
          if (res.data.code == "1") {
            this.count = res.data.data;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>
    
<style lang="less" scoped>
.box {
  width: 100%;
  height: auto;
  overflow: auto;
  .top {
    #rank {
      margin-left: 2vw;
      margin-top: 1vh;
    }
  }
  .bottom {
    margin-top: 1vh;
    width: 83vw;
    height: 20vh;
    border-radius: 10px;
    border: 1px solid #000;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: border-color 0.3s ease;

    &:hover {
      border-color: blue;
    }

    .major {
      font-size: 1.5vw;
      font-weight: bold;
      background-image: linear-gradient(
        to top,
        #dbdcd7 0%,
        #dddcd7 24%,
        #e2c9cc 30%,
        #e7627d 46%,
        #b8235a 59%,
        #801357 71%,
        #3d1635 84%,
        #1c1a27 100%
      );
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text;
    }

    .username {
      font-size: 1vw;
      margin-top: 2vh;
    }

    .year {
      font-size: 0.9vw;
      color: gray;
      margin-top: 2vh;
    }

    .time {
      font-size: 0.9vw;
      color: gray;
      margin-top: 2vh;
    }
    .leavewords {
      font-size: 1vw;
      margin-left: 13vw;
      width: 60vw;
      overflow: auto;
      word-wrap: break-word;
      height: 20vh;
      margin-top: -14vh;
    }
    .leavewords::-webkit-scrollbar {
      display: none;
    }
    .love {
      padding: 5px 10px;
      position: absolute;
      right: 17vw;
      color: gray;
      &.lovechange {
        color: red;
      }
    }

    .button {
      display: flex;
      justify-content: flex-end;
      align-items: center;

      button {
        background-color: blue;
        color: white;
        border: none;
        padding: 5px 10px;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: darkblue;
        }
      }
    }
  }
}
.buttonadd {
  background-color: #f66c6e;
  color: white;
  width: 3vw;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  position: fixed;
  right: 4vw;
  top: 11vh;
  &:hover {
    background-color: darkred;
    animation: rotate 5s infinite linear;
  }
}

@keyframes rotate {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
</style>