<template>
  <div>
    <div class="box">
      <div class="left">
        <div class="top">
          <img src="../assets/MyUserInfo/head.jpg" alt="" />
          <div class="username"></div>
        </div>
        <div
          class="userInfo"
          :class="userInfo ? 'active' : ''"
          @click="ToUserInfo()"
        >
          个人信息
        </div>
        <div class="message" @click="ToMyMessage()">我的消息</div>
        <div
          class="collector"
          :class="collector ? 'active' : ''"
          @click="ToMyCollector()"
        >
          我的收藏
        </div>
        <div
          class="blogger"
          :class="blogger ? 'active' : ''"
          @click="ToMyBlog()"
        >
          我的博客
        </div>
        <div
          class="leavewords"
          :class="leavewords ? 'active' : ''"
          @click="ToMyLeaveWords()"
        >
          我的留言
        </div>
        <div
          class="shareUrl"
          :class="shareUrl ? 'active' : ''"
          @click="ToMyShareUrl()"
        >
          我的网址
        </div>
        <div class="project" @click="ToMyproject()">我的项目</div>
        <div class="opinion" @click="ToMyopinion()">意见反馈处理</div>
      </div>
      <div class="right">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    userInfo() {
      if (this.$route.path == "/MyUserInfo") {
        return true;
      } else {
        return false;
      }
    },
    collector() {
      if (this.$route.path == "/MyUserInfo/MyCollector") {
        return true;
      } else {
        return false;
      }
    },
    leavewords() {
      if (this.$route.path == "/MyUserInfo/MyLeaveWords") {
        return true;
      } else {
        return false;
      }
    },
    shareUrl() {
      if (this.$route.path == "/MyUserInfo/MyShareUrl") {
        return true;
      } else {
        return false;
      }
    },
    blogger() {
      if (this.$route.path == "/MyUserInfo/MyBlog") {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    //跳转到我的收藏
    ToMyCollector(){
      if (this.$route.path != "/MyUserInfo/MyCollector") {
        this.$router.push("/MyUserInfo/MyCollector");
      }
    },
    //跳转到我的项目
    ToMyproject() {
      this.$notify.info({
        message: "暂不开放我的项目，抱歉",
        title: "消息提示",
        position: "top-left",
      });
    },
    //跳转到意见反馈处理
    ToMyopinion() {
      this.$notify.info({
        message: "暂不开放意见反馈处理，抱歉",
        title: "消息提示",
        position: "top-left",
      });
    },
    //跳转到我的消息
    ToMyMessage() {
      this.$notify.info({
        message: "暂不开放我的消息，抱歉",
        title: "消息提示",
        position: "top-left",
      });
    },
    //跳转我的博客
    ToMyBlog() {
      if (this.$route.path != "/MyUserInfo/MyBlog") {
        this.$router.push("/MyUserInfo/MyBlog");
      }
    },
    //跳转个人网址
    ToMyShareUrl() {
      if (this.$route.path != "/MyUserInfo/MyShareUrl") {
        this.$router.push("/MyUserInfo/MyShareUrl");
      }
    },
    //跳转个人信息
    ToUserInfo() {
      if (this.$route.path != "/MyUserInfo") {
        this.$router.push("/MyUserInfo");
      }
    },
    //跳转个人留言
    ToMyLeaveWords() {
      if (this.$route.path != "/MyUserInfo/MyLeaveWords") {
        this.$router.push("/MyUserInfo/MyLeaveWords");
      }
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  width: 99%;
  height: 89vh;
  position: fixed;
  top: 10vh;
  display: flex;
  .left {
    .top {
      margin-top: 1vh;
      display: flex;
      justify-content: space-evenly;
      img {
        width: 8vh;
        border: 1px solid gray;
        border-radius: 50%;
        height: 8vh;
      }
      .username {
        font-size: 10px;
      }
    }
    width: 20%;
    height: 100%;
    border: 2px solid #cee3ff;
    text-align: center;
    line-height: 10vh;
    .active {
      color: red;
    }
  }
  .right {
    width: 100%;
    height: 100%;
    border: 2px solid #cee3ff;
    margin-left: 1vh;
  }
}
</style>