<template>
  <div>
    <div>
      <el-button
        id="WebSocketMessage"
        circle=""
        icon="el-icon-chat-dot-round"
        type="danger"
        @click="clickGetMessage()"
        v-if="Onshow"
        plain
      ></el-button>
      <el-dialog
        :before-close="beforeClose"
        title="未读消息"
        :visible="visible"
      >
        <div v-for="(item, index) in messages" :key="index">
          <el-form>
            <el-form-item
              :label="
                item.user.major +
                ' ' +
                item.user.year +
                ' ' +
                item.user.username
              "
            >
              发来消息：
                {{ item.message }}
              
            </el-form-item>
          </el-form>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Onshow: false,
      visible: false,
      messages: [],
    };
  },
  mounted() {
    this.$bus.$on("unWebSocketMessage", () => {
      this.Onshow = true;
    });
    this.GetMessage();
  },
  methods: {
    //打开获取的数据
    clickGetMessage() {
      this.Onshow = false;
      this.visible = true;
      this.GetMessage();
    },
    //获取数据
    GetMessage() {
      this.$axios.get("/Ws/GetMessage?id=" + localStorage.getItem("id")).then(
        (res) => {
          if (res.data.code == "1") {
            this.messages = res.data.data;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //打开显示隐藏消息未读
    beforeClose() {
      this.$confirm("确认关闭 ？该消息不做保存在读取后。")
        .then(() => {
          this.visible = false;
          this.Onshow = false;
          this.$axios
            .get("/Ws/DeleteMessage?id=" + localStorage.getItem("id"))
            .then(() => {});
        })
        .catch(() => {});
    },
  },
};
</script>

<style lang="less" scoped>
#WebSocketMessage {
  position: fixed;
  z-index: 99999;
  right: 5%;
  top: 10%;
}
</style>