<template>
  <div>
    <div class="top">
      <div class="smallbox"></div>
      <div class="textInfo">个人信息</div>
      <div class="LogOut">
        <button @click="LogOut()">退出</button>
      </div>
    </div>
    <div class="bottom">
      <div class="username" @click.once="TipChangeInfo()">
        姓名；{{ user.username }}
      </div>
      <div class="password">
        密码：******
        <input
          type="password"
          v-if="showEdit"
          :value="user.password"
          ref="UserInfoPassword"
        />
      </div>
      <div class="college">学院：{{ user.college }}</div>
      <div class="major">专业：{{ user.major }}</div>
      <div class="year">年级：{{ user.year }}</div>
      <div class="email">
        邮箱：
        <span v-if="user.email != ''">{{ user.email }}</span>
        <span v-else>未录入</span>
        <input
          type="text"
          v-if="showEdit"
          :value="user.email"
          ref="UserInfoEmail"
        />
      </div>
      <div class="phone">
        电话：
        <span v-if="user.phone != ''">{{ user.phone }}</span>
        <span v-else>未录入</span>
        <input
          type="text"
          v-if="showEdit"
          :value="user.phone"
          ref="UserInfoPhone"
        />
      </div>
      <div class="wechat">
        微信号：<span v-if="user.wechat != ''">{{ user.wechat }}</span>
        <span v-else>未录入</span>
        <input
          type="text"
          :value="user.wechat"
          ref="UserInfoWechat"
          v-if="showEdit"
        />
      </div>
      <button @click="changeInfo()">{{ showEditButton }}信息</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showEdit: false,
      showEditButton: "修改",
      id: "",
      user: [],
    };
  },
  mounted() {
    this.GetUsreInfoOne();
  },
  methods: {
    //提示个人信息联系管理员修改
    TipChangeInfo() {
      this.$message.info({ message: "需修改请联系社团干部" });
    },
    //修改信息
    changeInfo() {
      if (this.showEditButton == "修改") {
        this.showEditButton = "保存";
        this.showEdit = !this.showEdit;
      } else {
        const password = this.$refs.UserInfoPassword.value;
        const email = this.$refs.UserInfoEmail.value;
        const phone = this.$refs.UserInfoPhone.value;
        const wechat = this.$refs.UserInfoWechat.value;
        let isModified = false;
        if (this.user.password !== password) {
          isModified = true;
        }
        if (this.user.email !== email) {
          isModified = true;
        }
        if (this.user.phone !== phone) {
          isModified = true;
        }
        if (this.user.wechat !== wechat) {
          isModified = true;
        }
        if (!isModified) {
          this.showEdit = !this.showEdit;
          this.showEditButton = "修改";
          return;
        }
        this.$axios
          .post("/User/ChangeMyUserInfo", {
            id: this.id,
            password: password,
            email: email,
            phone: phone,
            wechat: wechat,
          })
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.$message.success({ message: "修改成功" });
                this.showEditButton = "修改";
                this.GetUsreInfoOne();
                this.showEdit = !this.showEdit;
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
      }
    },
    //退出登录
    LogOut() {
      const loading = this.$loading({ text: "正在退出登录" });
      const id = localStorage.getItem("id");
      setTimeout(() => {
        this.$axios.get("/User/LogoutUserOne?id=" + id).then((res) => {
          if (res.data.code == "1") {
            loading.close();
            this.$router.replace("/");
            this.$message.info({ message: "退出登录" });
            localStorage.removeItem("id");
            localStorage.removeItem("token");
            if (localStorage.getItem("websocket") == null) {
              this.$websocket.close();
            }
          }
        });
      }, 1000);
    },
    //获取用户信息
    GetUsreInfoOne() {
      this.id = localStorage.getItem("id");
      this.$axios.get("/User/GetUsreInfoOne?id=" + this.id).then(
        (res) => {
          if (res.data.code == "1") {
            this.user = res.data.data;
            this.user.password = "";
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
.top {
  display: flex;
  margin: 2vw;
  width: 90%;
  height: 3.5vh;
  .smallbox {
    width: 0.5vw;
    height: 2vw;
    background-color: #2e8ad4;
  }
  .textInfo {
    margin-left: 1vw;
    color: #2e8ad4;
    line-height: 3.5vh;
  }
  .LogOut {
    position: absolute;
    right: 7vw;
    button {
      color: white;
      background-color: darkgrey;
      border: none;
      padding: 5px 10px;
      border-radius: 5px;
      margin-right: 1vw;
      margin-left: 0.5vw;
      cursor: pointer;
      transition: background-color 0.3s ease;
      &:hover {
        background-color: darkslategrey;
      }
    }
  }
}
.bottom {
  cursor: pointer;
  margin-left: 5vw;
  font-size: 1vw;
  line-height: 8vh;
  img {
    width: 1vw;
  }
  input {
    margin-left: 2vw;
    border: none;
    outline: none;
    border-bottom: 1px solid black;
  }
  button {
    position: absolute;
    right: 15vw;
    color: white;
    background-color: #c02e60;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    &:hover {
      background-color: #5e1446;
    }
  }
}
</style>