<template>
  <div>
    <div class="search">
      <input
        type="text"
        @keyup.enter="GetChooseShareUrlSearch()"
        v-model="search"
        placeholder="请输入搜索内容"
      />
    </div>
    <div class="scrollTop" v-if="scrollTopshow" @click="scrollTopNow()">
      <el-button
        circle
        type="primary"
        plain
        icon="el-icon-arrow-up"
      ></el-button>
    </div>
    <div class="classify">
      <select name="" @change="changeClassify()" ref="shareClassify">
        <option value="全部">全部</option>
        <option value="技术">技术</option>
        <option value="娱乐">娱乐</option>
        <option value="教育">教育</option>
        <option value="商业">商业</option>
        <option value="社交">社交</option>
        <option value="健身">健身</option>
        <option value="文化艺术">文化艺术</option>
        <option value="新闻媒体">新闻媒体</option>
      </select>
      <select
        name=""
        id=""
        @change="chaneClassifyTech()"
        ref="shareClassifyTech"
        v-if="showclassify"
      >
        <option value="JavaScript">JavaScript</option>
        <option value="Git">Git</option>
        <option value="Java">Java</option>
        <option value="Linux">Linux</option>
        <option value="Articles">Articles</option>
        <option value="C/C++">C/C++</option>
        <option value="phthon">phthon</option>
        <option value="php">php</option>
        <option value="MySQL">MySQL</option>
        <option value="安全">安全</option>
        <option value="攻防">攻防</option>
        <option value="资源" selected>资源</option>
        <option value="其它">其它</option>
      </select>
    </div>
    <div class="add">
      ==>
      <button class="addbutton" @click="AddShareUrl()">{{ addText }}</button>
      <button class="sort" @click="sortUrl()">热门</button>
    </div>
    <div v-if="shareList.length != 0">
      <div class="box">
        <div class="item" v-for="item in shareList" :key="item.uuid">
          <div class="content" @click="OpneWindowNew(item.content)">
            <span class="name">网址：</span>{{ item.content }}
          </div>
          <div class="title">
            <span class="name">详情：</span>{{ item.title }}
          </div>
          <div class="user">
            <div class="major">{{ item.major }}</div>
            -
            <div class="username">{{ item.username }}</div>
            <div
              class="love"
              @click="loveAddClick(item.uuid, item.loveid)"
              :class="loveChange(item.loveid) ? 'lovechange' : ''"
            >
              ❤{{ item.love }}
            </div>
          </div>
          <div class="button">
            <el-button
              icon="el-icon-star-off"
              @click="Enshrine(item.uuid)"
              type="warning"
            ></el-button>
            <el-button
              icon="el-icon-share"
              type="success"
              @click="ShareShare()"
            ></el-button>
            <el-popconfirm
              title="是否删除？"
              @confirm="DeleteShareUrl(item.uuid)"
            >
              <el-button
                type="danger"
                icon="el-icon-delete
            "
                v-if="id == item.id"
                slot="reference"
              >
              </el-button>
            </el-popconfirm>
            <el-button
              type="primary"
              @click="copyShareUrl(item.content)"
              icon="el-icon-thumb"
            ></el-button>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <el-empty></el-empty>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addText: "发布",
      skip: 1,
      shareList: [],
      showclassify: false,
      search: "",
      scrollTopshow: false,
      id: "",
      time: "",
      sort: "",
      AndSrollBar: true,
    };
  },
  mounted() {
    this.$bus.$on("AddShareUrl", () => {
      this.skip = 1;
      this.GetShareUrl();
    });
    this.GetShareUrl();
    this.AddEnenUserScoll();
    this.id = localStorage.getItem("id");
  },
  methods: {
    //分享网址
    ShareShare() {
      this.$notify.info({
        message: "暂不支持分享操作，抱歉",
        title: "消息提示",
      });
    },
    //处理用户收藏操作
    Enshrine(e) {
      this.$axios
        .get(`/ShareUrl/AddUserShareUrl?userId=${this.id}&shareUrlId=${e}`)
        .then((res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "收藏成功" });
          } else {
            this.$message.error({ message: res.data.msg });
          }
        });
    },
    //处理用户排序热门操作
    sortUrl() {
      this.skip = 1;
      this.sort = "排序";
      this.GetShareUrl();
    },
    //对用户点赞进行操作
    loveAddClick(a, b) {
      const time = Date.now();
      if (this.oldTime != "") {
        if (this.oldTime - time < 2000) {
          this.$message.warning({ message: "不要频繁操作" });
          return;
        }
      }
      const ifAdd = this.loveChange(b);
      if (ifAdd) {
        this.$axios
          .get(`/ShareUrl/DeleteShareUrlLoveid?uuid=${a}&id=${this.id}`)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.GetShareUrl();
                this.oldTime = Date.now();
                this.$message.info({ message: "(╯°□°）╯︵ ┻━┻" });
                this.oldTime = "";
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
      } else {
        this.$axios
          .get(`/ShareUrl/AddShareUrlLoveid?uuid=${a}&id=${this.id}`)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.GetShareUrl();
                this.oldTime = Date.now();
                this.$message.success({ message: "(¬‿¬)" });
                this.oldTime = "";
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
      }
    },
    //判断是否点赞高亮
    loveChange(e) {
      const reg = new RegExp(this.id, "g");
      if (reg.test(e)) {
        return true;
      } else {
        return false;
      }
    },
    //复制网址
    copyShareUrl(e) {
      if (navigator.clipboard && navigator.clipboard.writeText) {
        navigator.clipboard.writeText(e);
        this.$notify.success({ message: "复制成功", title: "消息" });
      } else {
        this.$message.warning({ message: "剪贴板权限未开,请手动粘贴" });
      }
    },
    //回到顶部
    scrollTopNow() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    //删除分享的网址
    DeleteShareUrl(e) {
      this.$axios.get("/ShareUrl/DeleteShareUrl?uuid=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.skip = 1;
            console.log(this.shareList);
            if (this.shareList.length == 1) {
              this.sort = "";
              this.search = "";
              this.AndSrollBar = true;
            }
            this.GetShareUrl();
          }
        },
        (err) => {
          console.log(err);
          alert("系统维护中...");
        }
      );
    },
    //用户搜索内容
    GetChooseShareUrlSearch() {
      if (this.search == "" || this.search[0] == "\\") return;
      this.skip = 1;
      this.GetShareUrl();
    },
    //判断用户选择
    chaneClassifyTech() {
      const chaneClassifyTech = this.$refs.shareClassifyTech.value;
      this.$axios
        .get(
          "/ShareUrl/GetChooseShareUrlClassify?classifyTechnology=" +
            chaneClassifyTech
        )
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.shareList = res.data.data;
              if (res.data.data.length == 0) {
                this.skip = 0;
              }
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        );
    },
    //判断用户选择隐藏
    changeClassify() {
      const shareClassify = this.$refs.shareClassify.value;
      if (shareClassify == "技术") {
        this.showclassify = true;
      } else {
        this.showclassify = false;
      }
      if (shareClassify == "全部") {
        this.GetShareUrl();
      } else {
        this.$axios
          .get("/ShareUrl/GetChooseShareUrl?classify=" + shareClassify)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.shareList = res.data.data;
                if (res.data.data.length == 0) {
                  this.skip = 0;
                }
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
      }
    },
    //打开新页面
    OpneWindowNew(e) {
      window.open(e);
    },
    //监听用户的滚轮高度
    AddEnenUserScoll() {
      window.addEventListener("scroll", () => {
        const containerHeight = document.documentElement.clientHeight;
        const scrollContainer = document.documentElement;
        const scrollHeight = scrollContainer.scrollHeight;
        const scrollTop = scrollContainer.scrollTop;
        if (scrollTop >= 1000) {
          this.scrollTopshow = true;
        } else {
          this.scrollTopshow = false;
        }
        if (scrollTop + containerHeight >= scrollHeight - 1) {
          if (this.AndSrollBar) {
            this.skip++;
            this.$axios
              .get(
                `/ShareUrl/GetShareUrl?skip=${this.skip}&sort=${this.sort}&search=${this.search}`
              )
              .then(
                (res) => {
                  if (res.data.code == "1") {
                    if (res.data.data.records.length == 0) {
                      this.AndSrollBar = false;
                      this.$message.info({ message: "没有更多了" });
                    } else {
                      const oldList = this.shareList;
                      this.shareList = [...oldList, ...res.data.data.records];
                    }
                  }
                },
                (err) => {
                  console.log(err);
                  this.$message.error({ message: "系统维护中..." });
                }
              );
          }
        }
      });
    },
    //页面组件刷新获取请求
    GetShareUrl() {
      this.$axios
        .get(
          `/ShareUrl/GetShareUrl?skip=${this.skip}&sort=${this.sort}&search=${this.search}`
        )
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.shareList = res.data.data.records;
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        );
    },
    //点击展示发布
    AddShareUrl() {
      this.$router.push("/ShareUrl/AddShareUrl");
    },
  },
};
</script>

<style lang="less" scoped>
.classify {
  right: 2vw;
  position: absolute;
  height: 2vh;
}
.add {
  right: 2vw;
  position: absolute;
  margin-top: 10vh;
  .addbutton {
    color: white;
    background-color: green;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 1vw;
    cursor: pointer;
    transition: background-color 0.3s ease;
    &:hover {
      background-color: darkgreen;
    }
  }
  .sort {
    color: white;
    background-color: rgb(184, 7, 7);
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 1vw;
    cursor: pointer;
    transition: background-color 0.3s ease;
    &:hover {
      background-color: rgb(92, 7, 7);
    }
    &:focus {
      background-color: red;
    }
  }
}
.search {
  input {
    right: 2vw;
    position: absolute;
    margin-top: 20vh;
    border: none;
    outline: none;
    border-bottom: 1px solid darkolivegreen;
  }
}
.scrollTop {
  right: 2vw;
  position: fixed;
  bottom: 10vh;
  cursor: pointer;
}
.box {
  width: 90%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  .item {
    border-bottom: 1px solid black;
    border-top: 1px solid black;
    width: 40%;
    margin: 1vh;
    height: auto;
    border-radius: 20px;
    overflow: auto;
    .name {
      color: red;
    }
    a {
      text-decoration: none;
    }
    .content {
      margin: 1vw;
      word-wrap: break-word;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      cursor: pointer;
    }
    .title {
      margin: 1vw;
      word-wrap: break-word;
      height: 20vh;
      overflow: auto;
    }
    .title::-webkit-scrollbar {
      display: none;
    }
    .user {
      margin-left: 1vw;
      display: flex;
      .love {
        margin-left: 1vw;
        color: gray;
        &.lovechange {
          color: red;
        }
      }
    }
    .button {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 2vh;
      button {
        padding: 5px 10px;
        margin-right: 1vw;
      }
    }
  }
  .item:hover {
    border-bottom: 1px solid darkblue;
    border-top: 1px solid darkred;
  }
  .item::-webkit-scrollbar {
    display: none;
  }
}
</style>