<template>
  <div class="blog">
    <div class="sidebar-left">
      <div class="sidebar-section">
        <ul class="category-list">
          <li
            v-for="category in categories"
            :key="category.id"
            @click="selectCategory(category.name)"
            :class="{
              active:
                activeCategory === category.name ||
                (activeCategory === null && category.name === '首页'),
            }"
          >
            {{ category.name }}
          </li>
          <div class="button">
            <button @click="ToBlogNew()">发布</button>
            <button @click="ShowTop()" style="background-color: darkred">
              {{ ShowTopText }}
            </button>
          </div>
        </ul>
      </div>
    </div>
    <main class="content">
      <section class="article-list" v-if="articles.length != 0">
        <article
          @click="
            ToBlogDetail(
              article.uuid,
              article.title,
              article.introduce,
              article.time,
              article.classify,
              article.love
            )
          "
          v-for="article in articles"
          :key="article.uuid"
          class="article"
        >
          <div class="article-content">
            <div class="article-image">
              <img
                :src="'http://8.130.43.227/images/' + article.img"
                alt="图片加载失败"
                :class="{ flipped: article.category }"
                v-if="article.img != ''"
              />
              <img
                src="../assets/image/backimg1.jpg"
                alt="图片加载失败"
                :class="{ flipped: article.category }"
                v-else
              />
            </div>
            <div class="article-details">
              <h2>{{ article.title }}</h2>
              <p>{{ article.introduce }}</p>
              <p>时间: {{ article.time | formdata }}</p>
              <p>分类: {{ article.classify }}</p>
              <p><i class="el-icon-view"></i>: {{ article.love }}</p>
            </div>
          </div>
        </article>
      </section>
      <section style="margin: auto" v-else>
        <el-empty description="没有内容"></el-empty>
      </section>
      <aside class="sidebar">
        <div class="sidebar-section">
          <h3>个人信息</h3>
          <p>姓名：{{ userInfo[0] }}</p>
          <p>专业：{{ userInfo[1] }}</p>
          <p>发表文章数量：{{ userInfo[2] }}</p>
        </div>
        <div class="sidebar-section">
          <h3>标签</h3>
          <ul>
            <li>·<el-tag type="success" closable>前端</el-tag></li>
            <li style="margin-top: 2vh">
              ·<el-tag type="primary" closable>后端</el-tag>
            </li>
          </ul>
        </div>
        <div class="sidebar-section">
          <h3>搜索</h3>
          <input
            type="text"
            @keyup.enter="SearchBlog()"
            v-model="search"
            class="search-input"
            placeholder="全局搜索"
          />
        </div>
        <div class="sidebar-section">
          <h3>文章总数目: {{ total }}</h3>
          <el-button @click="openColorPicker()">取色器</el-button>
          {{ color }}
        </div>
        <div>
          <el-rate v-model="RateColor" disabled :colors="colors"> </el-rate>
        </div>
        <div id="sakana-a"></div>
      </aside>
    </main>
    <div class="page">
      <el-pagination
        :total="total"
        @current-change="currentChange"
      ></el-pagination>
    </div>
    <footer class="footer">
      <p>版权信息 © 软件协会博客</p>
    </footer>
    <router-view></router-view>
  </div>
</template>
  
  <script>
import "sakana-widget/lib/index.css";
import SakanaWidget from "sakana-widget";
export default {
  data() {
    return {
      articles: [],
      categories: [
        { id: 1, name: "首页" },
        { id: 2, name: "JavaScript" },
        { id: 3, name: "Java" },
        { id: 4, name: "Git" },
        { id: 5, name: "MySQL" },
        { id: 6, name: "Linux" },
        { id: 7, name: "攻防" },
        { id: 8, name: "安全" },
        { id: 9, name: "其它" },
        { id: 10, name: ">>" },
      ],
      ShowTopText: "隐藏",
      activeCategory: "首页",
      scrollTopVisible: false,
      skip: 1,
      search: "",
      userInfo: [],
      color: "",
      colors: ["#99A9BF", "#F7BA2A", "#FF9900"],
      RateColor: 4.3,
      total: 0,
    };
  },
  filters: {
    formdata(timetemp) {
      const now = new Date(+timetemp).toLocaleString();
      return now;
    },
  },
  mounted() {
    let id = localStorage.getItem("id");
    if (id != null) {
      this.ShowTop();
      this.GetBlogAll();
      this.GetBlogUserCount();
    }
    this.github();
  },
  methods: {
    github() {
      const github = SakanaWidget.getCharacter("chisato");
      github.image = require("../assets/Index/userchat.jpg");
      SakanaWidget.registerCharacter("github", github);
      new SakanaWidget({ character: "github" }).mount("#sakana-a");
    },
    //改变total页数，翻页
    currentChange(e) {
      this.skip = e;
      this.GetBlogAll();
    },
    //改变背景颜色
    switchBackgroundColor() {},
    //取色器的使用
    async openColorPicker() {
      if ("EyeDropper" in window) {
        const dropper = new window.EyeDropper();
        try {
          const result = await dropper.open();
          this.color = result.sRGBHex;
          this.$notify.success({
            message: "取色成功，已自动复制",
            title: "消息提示",
          });
          navigator.clipboard.writeText(this.color);
        } catch {
          this.$message.info({ message: "取色失败" });
        }
      } else {
        this.$message.info({ message: "浏览器不支持取色" });
      }
    },
    //用户选择分类
    selectCategory(categoryName) {
      this.activeCategory = categoryName;
      this.GetBlogAll(categoryName);
    },
    //处理用户获取自己的文章数量和自己的详情
    GetBlogUserCount() {
      const id = localStorage.getItem("id");
      this.$axios.get("/Blog/GetBlogUserCount?id=" + id).then(
        (res) => {
          if (res.data.code == "1") {
            this.userInfo = res.data.data;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //搜索文章详情
    SearchBlog() {
      if (this.search == "" || this.search[0] == "\\") return;
      this.GetBlogAll();
    },
    //跳转详情页并让浏览量更新
    ToBlogDetail(e, title, introduce, time, classify, love) {
      this.$axios.get("/Blog/BlogDetailAdd?id=" + e);
      this.$router.push({
        path: "/BlogDetail",
        query: {
          detailId: e,
          title: title,
          introduce: introduce,
          time: time,
          classify: classify,
          love: love,
        },
      });
    },
    //跳转发布页面
    ToBlogNew() {
      this.$router.push("/BlogNew");
    },
    //获取文章详情
    GetBlogAll(categoryName) {
      if (categoryName == undefined) {
        categoryName = "";
      }
      this.$axios
        .get(
          `/Blog/GetBlogAll?skip=${this.skip}&search=${this.search}&categoryName=${categoryName}`
        )
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.articles = res.data.data.records;
              this.total = res.data.data.total;
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "系统维护中..." });
          }
        );
    },

    //判断是否展示上面的top栏
    ShowTop() {
      if (this.ShowTopText == "其它") {
        this.ShowTopText = "隐藏";
      } else {
        this.ShowTopText = "其它";
      }
      this.$bus.$emit("ShowTop");
    },
  },
};
</script>
  
  <style lang="less" scoped>
.blog {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #f2f2f2;

  .sidebar-left {
    flex: 0 0 15%;
    padding: 10px;
    background-color: #f9f9f9;

    .sidebar-section {
      margin-bottom: 20px;
    }

    .category-list {
      list-style: none;
      padding: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .button {
        width: 40%;
        button {
          float: right;
          color: white;
          background-color: darkgreen;
          border: none;
          padding: 5px 10px;
          border-radius: 5px;
          margin-right: 1vw;
          margin-left: 0.5vw;
          cursor: pointer;
          transition: background-color 0.3s ease;

          &:hover {
            background-color: darkslategrey;
          }
        }
      }
    }

    .category-list li {
      cursor: pointer;
      padding: 5px;
      background-color: darkblue;
      color: white;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s ease;

      &:hover {
        background-color: darkred;
        color: #fff;
      }

      &.active {
        background-color: darkred;
        color: #fff;
      }
    }
  }

  .content {
    flex: 1;
    display: flex;
    padding: 20px;
    overflow: auto;
  }

  .article-list {
    flex: 1 1 70%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 20px;
    height: 100%;
  }

  .article {
    border-radius: 20px;
    border: 1px solid #d7d2cc;
    background-color: #fff;
    transition: border-color 0.3s;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    &:hover {
      border-color: #00a0e9;
    }

    .article-content {
      .article-image {
        img {
          width: 100%;
          max-height: 200px;
          object-fit: cover;
          transform: scale(1);
          transition: transform 0.3s;
        }
      }

      .article-details {
        margin-top: 10px;

        h2 {
          font-size: 1.2rem;
          margin-top: 0;
          margin-bottom: 10px;
          transform: scale(1);
          transition: transform 0.3s;
          margin-left: 1vw;
        }

        p {
          overflow: hidden;
        }
      }
    }

    &:hover {
      .article-content {
        .article-image img {
          transform: scale(1.05);
        }

        .article-details h2 {
          transform: scale(1.1);
        }
      }
    }
  }

  .sidebar {
    flex: 0 0 25%;
    margin-left: 20px;
    padding: 20px;
    background-color: #f9f9f9;

    .sidebar-section {
      margin-bottom: 20px;
      input {
        width: 90%;
        height: 3vh;
        border-radius: 10px;
        outline: none;
        border: 1px solid gray;
        padding: 10px;
        &:focus {
          border: 1px solid darkblue;
        }
      }

      h3 {
        margin-bottom: 10px;
      }

      ul {
        padding: 0;

        li {
          list-style: none;
        }
      }
    }
  }
  .page {
    margin: auto;
    padding: 20px;
  }

  .footer {
    padding: 20px;
    color: #777;
    font-size: 0.5vw;
    text-align: right;
  }
}
</style>