<template>
  <div>
    <div class="box" v-if="blog.length != 0">
      <div
        class="item"
        @click="ToBlogDetail(item.uuid)"
        v-for="item in blog"
        :key="item.uuid"
      >
        <div class="img">
          <img
            :src="'http://8.130.43.227/images/' + item.img"
            alt="图片加载失败"
            v-if="item.img != ''"
          />
          <img src="../assets/image/backimg1.jpg" alt="图片加载失败" v-else />
        </div>

        <div class="title">{{ item.title }}</div>
        <div class="introduce">
          {{ item.introduce }}
        </div>
        <div class="classify">分类：{{ item.classify }}</div>
        <div class="love">❤：{{ item.love }}</div>
        <div class="delete">
          <el-popconfirm title="是否删除？" @confirm="DeleteMyBlog(item.uuid)">
            <el-button
              type="primary"
              slot="reference"
              @click.stop="None()"
              style="float: rigth"
              >删除</el-button
            >
          </el-popconfirm>
        </div>
      </div>
    </div>
    <div v-else>
      <el-empty description="没有内容"></el-empty>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blog: [],
    };
  },
  methods: {
    //None阻止跳转
    None() {},
    //跳转详情页
    ToBlogDetail(e) {
      this.$router.push({
        path: "/BlogDetail",
        query: {
          detailId: e,
        },
      });
    },
    //处理用户删除自己的博客
    DeleteMyBlog(e) {
      this.$axios.get("/Blog/DeleteMyBlog?uuid=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "删除成功" });
            this.blog = [];
            this.GetMyblogInfo();
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
    //处理用户获取自己的博客
    GetMyblogInfo() {
      const id = localStorage.getItem("id");
      this.$axios.get("/Blog/GetMyblogInfo?id=" + id).then(
        (res) => {
          if(res.data.code == "1"){
            if (res.data.data.length != 0) {
            this.blog = res.data.data;
          }
          }

        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
  mounted() {
    this.GetMyblogInfo();
  },
};
</script>

<style lang="less" scoped>
.box {
  width: 100%;
  height: 89vh;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  overflow: auto;
  .item {
    width: 40%;
    height: auto;
    margin: 1vh;
    border: 1px solid black;
    border-radius: 10px;
    img {
      width: 90%;
      margin: 5%;
      height: 30vh;
      margin-bottom: 2vh;
    }
    .title {
      font-size: 2vw;
      margin: 1vh;
      margin-bottom: 2vh;
    }
    .introduce {
      margin: 1vh;
      font-size: 1vw;
      margin-bottom: 2vh;
    }
    .classify {
      margin: 1vh;
      font-size: 1vw;
      margin-bottom: 2vh;
    }
    .love {
      margin: 1vh;
      font-size: 1vw;
      margin-bottom: 2vh;
    }
    .delete {
      width: 100%;
      height: 10vh;
      button {
        float: right;
        margin-right: 1vw;
      }
    }

    &:hover {
      border: 1px solid blue;
    }
  }
}
.box::-webkit-scrollbar {
  display: none;
}
</style>