import router from "vue-router";
import AssociationWindowLogin from "../pages/AssociationWindowLogin";
import AssociationWindowSign from "../pages/AssociationWindowSign";
import AssociationIndex from "../pages/AssociationIndex";
import AssociationAdmin from "../pages/AssociationAdmin";
import IfWindowMobile from "../pages/IfWindowMobile";
import AdminRightUser from "../components/AdminRightUser";
import AdminRightAdminUser from "../components/AdminRightAdminUser";
import AdminRightLeaveWords from "../components/AdminRightLeaveWords";
import AdminBlog from "../components/AdminBlog";
import AdminShareUrl from "../components/AdminShareUrl";
import IndexBottomLeaveWords from "../components/IndexBottomLeaveWords";
import MyUserInfo from "../components/MyUserInfo";
import ShareUrl from "../components/ShareUrl";
import MyInfo from "../components/MyInfo";
import MyLeaveWords from "../components/MyLeaveWords";
import AssociationOpinion from "../components/AssociationOpinion";
import MyShareUrl from "../components/MyShareUrl";
import AssociationiRecurit from "../components/AssociationiRecurit";
import IndexBlog from "../components/IndexBlog";
import BlogNew from "../pages/BlogNew";
import BlogDetail from "../pages/BlogDetail";
import MyBlog from "../components/MyBlog";
import AddLeaveWord from "@/components/AddLeaveWord.vue";
import AddBlogNew from "@/components/AddBlogNew.vue";
import AddShareUrl from "@/components/AddShareUrl.vue";
import AdminRightUserEdit from "@/components/AdminRightUserEdit.vue";
import WarningTip from "@/components/WarningTip.vue";
import MyCollector from "@/components/MyCollector.vue";
import ChatBugHelp from "@/components/ChatBugHelp.vue";
import UserChat from "@/components/UserChat.vue";
import AddBugHelp from "@/components/AddBugHelp.vue";
import BugHelpDetail from "../pages/BugHelpDetail.vue";
import AddSaveMessage from "../components/AddSaveMessage.vue";

export default new router({
  routes: [
    {
      path: "/",
      component: AssociationWindowLogin,
    },
    {
      path: "/AssociationWindowSign",
      component: AssociationWindowSign,
    },
    {
      path: "/AssociationIndex",
      component: AssociationIndex,
      children: [
        {
          path: "/",
          redirect: "IndexBlog",
        },
        {
          path: "IndexBlog",
          component: IndexBlog,
          children: [{ path: "WarningTip", component: WarningTip }],
        },
        {
          path: "/IndexBottomLeaveWords",
          component: IndexBottomLeaveWords,
          children: [{ path: "AddLeaveWord", component: AddLeaveWord }],
        },
        {
          path: "/MyUserInfo",
          component: MyUserInfo,
          children: [
            {
              path: "/",
              component: MyInfo,
            },
            {
              path: "MyLeaveWords",
              component: MyLeaveWords,
            },
            {
              path: "MyShareUrl",
              component: MyShareUrl,
            },
            {
              path: "MyBlog",
              component: MyBlog,
            },
            {
              path: "MyCollector",
              component: MyCollector,
            },
          ],
        },
        {
          path: "/ShareUrl",
          component: ShareUrl,
          children: [{ path: "AddShareUrl", component: AddShareUrl }],
        },
        {
          path: "/AssociationOpinion",
          component: AssociationOpinion,
        },
        {
          path: "/AssociationiRecurit",
          component: AssociationiRecurit,
        },
        {
          path: "/ChatBugHelp",
          component: ChatBugHelp,
          children: [
            { path: "UserChat", component: UserChat },
            { path: "AddBugHelp", component: AddBugHelp },
            {
              path: "AddSaveMessage",
              component: AddSaveMessage,
            },
          ],
        },
      ],
    },
    {
      path: "/AssociationAdmin",
      component: AssociationAdmin,
      children: [
        {
          path: "/",
          component: AdminRightUser,
          children: [
            { path: "AdminRightUserEdit", component: AdminRightUserEdit },
          ],
        },
        {
          path: "/AdminRightAdminUser",
          component: AdminRightAdminUser,
        },
        {
          path: "/AdminRightLeaveWords",
          component: AdminRightLeaveWords,
        },
        {
          path: "/AdminBlog",
          component: AdminBlog,
        },
        {
          path: "/AdminShareUrl",
          component: AdminShareUrl,
        },
      ],
    },
    {
      path: "/BlogNew",
      component: BlogNew,
      children: [{ path: "AddBlogNew", component: AddBlogNew }],
    },
    {
      path: "/IfWindowMobile",
      component: IfWindowMobile,
    },
    {
      path: "/BlogDetail",
      component: BlogDetail,
    },
    {
      path: "/BugHelpDetail",
      component: BugHelpDetail,
    },
  ],
});
