<template>
  <div>
    <el-dialog
      :visible.sync="visible"
      :before-close="beforeClose"
      width="80%"
      fullscreen
      title="您正在进行私聊..."
    >
      <el-card id="messageContainer" style="overflow: auto">
        <div style="height: 49vh">
          <div v-for="(message, index) in messages" :key="index">
            <div :class="message.sender === 'self' ? 'leftUser' : 'rightUser'">
              <el-avatar
                shape="square"
                v-if="message.sender === 'self'"
                :src="imgUrl"
                style="margin-right: 12px"
              ></el-avatar>
              <span class="message-content">{{ message.content }}</span>
              <el-avatar
                shape="square"
                v-if="message.sender === 'other'"
                :src="imgUrl"
                style="margin-left: 12px"
              ></el-avatar>
            </div>
          </div>
        </div>
      </el-card>
      <div
        style="
          margin-top: 1vh;
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: auto;
        "
        class="inputDeep"
        @keydown.enter.prevent="SendMessage()"
      >
        <el-input
          type="textarea"
          rows="6"
          maxlength="1000"
          show-word-limit
          resize="none"
          placeholder="请输入内容"
          v-model="userChat"
          @input="sensitiveWord"
        ></el-input>
        <div style="margin-left: 2%; display: flex; align-items: center">
          <el-button type="primary" size="mini" @click="RemoveMessageList()"
            >清屏</el-button
          >
          <el-button size="mini" type="success" @click="SendMessage()"
            >发送</el-button
          >
        </div>
      </div>
    </el-dialog>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      visible: true,
      userChat: "",
      id: "",
      websocket: "",
      messages: [],
      abcd: "0",
      imgUrl: require("../assets/Index/userchat.jpg"),
      setinterval: null,
    };
  },
  mounted() {
    //路由回退
    this.$bus.$on("closeUserChatBus", () => {
      this.abcd = "1";
      this.$router.back("/ChatBugHelp");
    });
    //消息推送
    this.$bus.$on("UserChatBus", (e) => {
      this.$nextTick(() => {
        setTimeout(() => {
          this.messageContainer();
        }, 100);
      });
      this.messages.push({ sender: "self", content: e });
    });
    this.id = localStorage.getItem("id");
    //消息提示
    this.setinterval = setInterval(() => {
      this.$message.warning({
        message: "软件协会提醒您，请注意您的财产和生命安全",
      });
    }, 120000);
  },
  methods: {
    //清空历史记录
    RemoveMessageList() {
      this.messages = [];
    },
    //容器置底
    messageContainer() {
      const container = document.getElementById("messageContainer");
      if (container) {
        container.scrollTop = container.scrollHeight - container.clientHeight;
      }
    },
    //规则校验是否涉及敏感词
    sensitiveWord() {
      const reg =
        /微信号|QQ号|vx|手机号|额|钱|金|money|元|圆|yuan|园|weixin|wx|微信|金钱|qq|QQ|V|WEIXIN|WX|weixinhao|手机/gi;
      if (reg.test(this.userChat)) {
        this.userChat = this.userChat.replace(reg, "*");
      }
    },
    // 发送消息给服务器进行推送
    SendMessage() {
      if (this.$websocket) {
        if (this.userChat === "") {
          this.$message.warning({ message: "不能输入空" });
          return;
        }
        const chatId = this.$route.query.chatId;
        const message = {
          to: chatId,
          content: this.userChat,
        };
        this.$websocket.send(JSON.stringify(message));
        this.messages.push({ sender: "other", content: this.userChat });
        this.$nextTick(() => {
          this.messageContainer();
        });
        this.userChat = "";
      }
    },
    // 路由回退
    beforeClose() {
      this.$router.back();
    },
  },
  // 用户回退路由进行提示
  beforeRouteLeave(to, from, next) {
    if (this.abcd == "0") {
      if (confirm("聊天记录将不会被进行保留")) {
        if (this.$route.path == "/ChatBugHelp/UserChat") {
          const chatId = this.$route.query.chatId;
          const message = {
            to: chatId,
            content: "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj退出私聊",
          };
          this.$websocket.send(JSON.stringify(message));
          to.query.temp = "/ChatBugHelp";
          clearInterval(this.setinterval);
        }
        next();
      } else {
        next(false);
      }
    } else if (this.abcd == "1") {
      clearInterval(this.setinterval);
      this.$message.warning({ message: "对方已退出私聊" });
      next();
    }
  },
};
</script>
  
  <style lang="less" scoped>
.inputDeep /deep/ .el-textarea__inner {
  border: 1px solid #f2f2f2 !important;
}

.leftUser,
.rightUser {
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
}
.el-card::-webkit-scrollbar {
  display: none;
}

.leftUser {
  justify-content: flex-start;
}

.rightUser {
  justify-content: flex-end;
}

.message-content {
  margin-bottom: 2%;
  padding: 8px;
  background-color: #f0f0f0;
  border-radius: 6px;
  max-width: 70%;
  margin-top: 2%;
}

.rightUser .message-content {
  background-color: #e1f5fe;
  color: #333;
}
</style>
  