<template>
  <div>
    <div class="login-container">
      <div style="padding-top: 20px" @click="Tologin()">
        <i class="el-icon-caret-left"></i>
        <i class="el-icon-caret-left"></i>
        <i class="el-icon-caret-left"></i>
      </div>
      <el-card shadow class="login-card">
        <img
          class="logo"
          src="../assets/AssociationLogin/back.png"
          alt="Logo"
        />
        <el-select
          v-model="college"
          placeholder="请填写学院"
          class="login-select"
          @change="SelectCollege()"
          clearable
        >
          <el-option
            label="信息工程学院"
            selected
            value="信息工程学院"
          ></el-option>
          <el-option label="水利工程学院" value="水利工程学院"></el-option>
          <el-option label="电力工程学院" value="电力工程学院"></el-option>
          <el-option label="建筑工程学院" value="建筑工程学院"></el-option>
          <el-option label="自动化工程学院" value="自动化工程学院"></el-option>
          <el-option label="经济管理学院" value="经济管理学院"></el-option>
          <el-option label="马克思主义学院" value="马克思主义学院"></el-option>
          <el-option label="公共基础部" value="公共基础部"></el-option>
        </el-select>
        <el-select
          v-model="major"
          placeholder="请填写专业"
          class="login-select"
          clearable
          v-if="!showTextSelect"
          @change="changeMajorIt()"
        >
        <el-option label="软件技术" value="软件技术"></el-option>
        <el-option label="计算机应用技术" value="计算机应用技术"></el-option>
        <el-option label="移动互联应用技术" value="移动互联应用技术"></el-option>
        <el-option label="人工智能技术应用" value="人工智能技术应用"></el-option>
        <el-option label="大数据技术" value="大数据技术"></el-option>
        <el-option
          label="电子信息工程技术"
          value="电子信息工程技术"
          clearable
        ></el-option>
        <el-option label="通信技术" value="通信技术"></el-option>
        <el-option label="会计信息管理" value="会计信息管理"></el-option>
        <el-option label="其它" value="其它"></el-option>
        </el-select>
        <el-select
          v-model="year"
          placeholder="请填写入学年份"
          clearable
          class="login-select"
        >
          <el-option label="2015级" value="2015"></el-option>
          <el-option label="2016级" value="2016"></el-option>
          <el-option label="2017级" value="2017"></el-option>
          <el-option label="2018级" value="2018"></el-option>
          <el-option label="2019级" value="2019"></el-option>
          <el-option label="2020级" value="2020"></el-option>
          <el-option label="2021级" value="2021"></el-option>
          <el-option label="2022级" value="2022"></el-option>
          <el-option label="2023级" value="2023"></el-option>
          <el-option disabled label="2024级" value="2024"></el-option>
          <el-option disabled label="2025级" value="2025"></el-option>
        </el-select>
        <div @keyup.enter="GetUsernameFocus()">
          <el-input
            v-model="major"
            placeholder="请填写专业"
            class="login-input"
            v-if="showTextSelect"
            prefix-icon="el-icon-help"
            clearable
          />
        </div>
        <div @keyup.enter="GetPasswordFocus()">
          <el-input
            v-model="username"
            class="login-input"
            placeholder="请输入姓名"
            clearable
            maxlength="6"
            show-word-limit
            prefix-icon="el-icon-s-check"
            ref="signUsername"
          />
        </div>
        <div @keyup.enter="signClick()">
          <el-input
            v-model="password"
            class="login-input"
            placeholder="请输入密码"
            type="password"
            prefix-icon="el-icon-lock"
            ref="signPasswrod"
            maxlength="10"
            clearable
            show-password
          />
        </div>

        <div class="agreement">
          <el-checkbox
            v-model="checkboxGPL"
            label="Comply with the GPL"
            class="agree-checkbox"
          ></el-checkbox>
        </div>
        <div class="button">
          <el-button
            @click="signClick()"
            :disabled="InDisable()"
            class="login-button"
            >{{ SignText }}</el-button
          >
        </div>
      </el-card>
    </div>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      showTextSelect: false,
      college: "信息工程学院",
      year: "2023",
      username: "",
      password: "",
      major: "软件技术",
      checkboxGPL: true,
      SignText: "Sign up",
    };
  },
  mounted() {},
  methods: {
    //用户注册跳转
    Tologin() {
      this.$router.replace("/");
    },
    //获取焦点
    GetUsernameFocus() {
      this.$refs.signUsername.focus();
    },
    GetPasswordFocus() {
      this.$refs.signPasswrod.focus();
    },
    //用户注册
    signClick() {
      if (
        this.SignText == "Be registering" ||
        this.SignText == "Sign Successfully"
      )
        return;
      let bool = this.InDisable();
      if (bool) {
        this.$notify.warning({ message: "填写正确信息", title: "消息警告" });
        return;
      }
      this.SignText = "Be registering";
      const reg = /[\u4e00-\u9fa5]/g;
      if (!reg.test(this.username) || !reg.test(this.major)) {
        this.$message.warning({ message: "请输入正确的姓名或专业" });
        this.SignText = "Sign up";
        return;
      }
      this.$axios
        .post("/User/Sign", {
          username: this.username,
          password: this.password,
          college: this.college,
          major: this.major,
          year: this.year,
        })
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.SignText = "Sign Successfully";
              localStorage.setItem("id", res.data.data);
              localStorage.setItem("token", res.data.token);
              this.$message.success({
                message: "注册成功",
                iconClass: "el-icon-s-promotion",
                center: true,
                duration: 500,
              });
              localStorage.setItem("LoginOne", "LoginOne");
              setTimeout(() => {
                this.$router.replace("/AssociationIndex");
                this.$bus.$emit("openWebSocket")
              }, 500);
            } else {
              this.SignText = "Sign up";
              this.$message.info({ message: "该账号已注册" });
            }
          },
          (err) => {
            this.$message.error({ message: "系统维护中..." });
            console.log(err);
          }
        );
    },
    //选择其它
    changeMajorIt() {
      if (this.major == "其它") {
        this.$notify.info({ message: "请联系社团干部", title: "消息提示" });
      }
    },
    //判断是否按钮禁用
    InDisable() {
      if (this.showTextSelect == false) {
        if (
          this.password == "" ||
          this.username == "" ||
          this.checkboxGPL == false
        ) {
          return true;
        }
      } else {
        if (
          this.username == "" ||
          this.password == "" ||
          this.major == "" ||
          this.checkboxGPL == false
        ) {
          return true;
        }
      }
    },
    //判断用户选择
    SelectCollege() {
      if (this.college != "信息工程学院") {
        this.major = "";
        this.showTextSelect = true;
      } else {
        this.major = "软件技术";
        this.showTextSelect = false;
      }
    },
  },
};
</script>
  
<style lang="less" scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f2f2f2;
  position: fixed;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  z-index: 999;
}

.login-card {
  width: 400px;
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
}

.logo {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.login-select {
  width: 100%;
  margin-bottom: 15px;
}

.login-input {
  width: 100%;
  margin-bottom: 15px;
  border-radius: 4px;
}

.agreement {
  display: flex;
  justify-content: center;
}

.agree-checkbox {
  margin-right: 5px;
}

.login-button {
  width: 100%;
  margin-top: 10px;
}
</style>