<template>
  <div class="app">
    <router-view></router-view>
    <WebSocket></WebSocket>
    <WebSocketMessage></WebSocketMessage>
  </div>
</template>

<script>
import WebSocket from "./components/WebSocket";
import WebSocketMessage from "./components/WebSocketMessage";

export default {
  name: "App",
  components: { WebSocket,WebSocketMessage },
  mounted() {
    document.title = "软件协会";
    this.DontPreventDefault();
    this.IfWindowMobile();
  },
  methods: {
    //禁止默认行为
    DontPreventDefault() {
      document.addEventListener("keyup", function (e) {
        if (e.key == "F12") {
          console.log("您已打开控制台，请遵循GPL协议");
        }
      });
    },
    //判断用户使用的设备
    IfWindowMobile() {
      var userAgent = navigator.userAgent;
      var isMobile =
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          userAgent
        );
      if (isMobile) {
        this.$router.replace("/IfWindowMobile");
      }
    },
  },
  beforeDestroy() {
    this.DontPreventDefault();
  },
};
</script>

