* {
  font-family: cursive;
}
.app {
  cursor: none;
  width: 100%;
  height: 100%;
  cursor: url("../src/assets/public/cursor-default.9c62a210.svg") 2 2, auto;
  font-family: "Courier New", Courier, monospace;
}
