<template>
  <div>
    <div class="box">
      <div class="top"><IndexTop :ifAdmin="ifAdmin"/></div>
      <div class="center">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import IndexTop from "@/components/IndexTop.vue";
export default {
  components: { IndexTop },
  data() {
    return {
      id:"",
      showWarningTip: true,
      ifAdmin:false
    };
  },
  mounted() {
    this.id = localStorage.getItem("id");
    this.IfLogin();
    this.ToAdmin()
  },
  methods: {
    //跳转管理员页面
    ToAdmin() {
      this.$axios.get("/User/IfAdmin?id=" + this.id).then(
        (res) => {
          if (res.data.code == "1") {
            this.ifAdmin = true
          } else if (res.data.code == "0") {
            this.ifAdmin = false;
          }
        },
        (err) => {
          this.$message.error({ message: "系统维护中..." });
          console.log(err);
        }
      );
    },
    //判读是否第一次登录
    LoginOne() {
      const LoginOne = localStorage.getItem("LoginOne");
      if (LoginOne != null) {
        if (this.$route.path !== "/AssociationIndex/IndexBlog/WarningTip") {
          // 只有在不是当前路由时才进行导航
          this.$router.push("/AssociationIndex/IndexBlog/WarningTip");
        }
      }
    },
    //判断是否登录
    IfLogin() {
      const id = localStorage.getItem("id");
      if (id == null) {
        this.$alert("请登录", "软件协会提示：", {
          showClose: false,
        })
          .then(() => {
            if (this.$route.path != "/") {
              this.$router.replace("/");
            }
          })
          .catch(() => {
            if (this.$route.path != "/") {
              this.$router.replace("/");
            }
          });
      } else {
        this.LoginOne();
      }
    },
  },
};
</script>

