<template>
  <div>
    <div class="box">
      <div class="item">
        <table>
          <thead>
            <tr>
              <th></th>
              <th>姓名</th>
              <th>密码</th>
              <th>学院</th>
              <th>专业</th>
              <th>年级</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in user" :key="item.id">
              <td>
                {{ index + 1 }}
              </td>
              <td>
                <div class="username">{{ item.username }}</div>
              </td>
              <td>
                <div class="password">{{ item.password }}</div>
              </td>
              <td>
                <div class="college">{{ item.college }}</div>
              </td>
              <td>
                <div class="major">{{ item.major }}</div>
              </td>
              <td>
                <div class="year">{{ item.year }}</div>
              </td>
              <td>
                <div class="button">
                  <el-button
                    class="delete"
                    type="danger"
                    plain
                    @click="AdminDeleteAdminUser(item.id)"
                  >
                    删除
                  </el-button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: [],
      id: "",
    };
  },
  mounted() {
    this.GetAdminUserAll();
  },
  methods: {
    //删除管理员
    AdminDeleteAdminUser(e) {
      this.$prompt("请输入管理员密码", "消息提示")
        .then((value) => {
          if (value == null) return;
          this.$axios
            .get(
              `/User/AdminDeleteAdminUser?adminpassword=${value.value}&id=${e}`
            )
            .then(
              (res) => {
                console.log(res);
                if (res.data.code == "1") {
                  this.GetAdminUserAll();
                  this.$message.success({ message: "删除成功" });
                } else {
                  this.$message.error({ message: "删除失败" });
                }
              },
              (err) => {
                console.log(err);
                this.$message.error({ message: "系统维护中..." });
              }
            );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //获取全部管理员
    GetAdminUserAll() {
      this.$axios.get("/User/GetAdminUserAll").then(
        (res) => {
          if(res.data.code == "1"){
            this.user = res.data.data;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  width: 85vw;
  height: 89vh;
  overflow: auto;
  margin-left: 15vw;
  margin-top: -89vh;
  .item {
    overflow: auto;
    table {
      width: 85vw;
      border-collapse: collapse;
      text-align: center;

      th,
      td {
        padding: 20px;
      }

      th {
        background-color: #f2f2f2;
        font-weight: bold;
      }
    }
  }
}
</style>