<template>
  <div>
    <div class="box">
      <div class="title">宏创未来软件开发协会成员纳新</div>
      <div class="left">
        <div
          id="imageContainer"
          @mouseover="mouseoverImg()"
          @mouseout="mouseoutImg()"
        >
          <img
          id="myImage"
            src="../assets/Index/recuritimg.jpg"
            alt="图片加载失败"
            title="软件协会宣传部"
          />
        </div>
      </div>
      <div class="right">
        <el-card style="height: 60vh">
          <el-empty></el-empty>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showActive: 0,
    };
  },
  methods: {
    mouseoverImg() {
      var image = document.getElementById("myImage");
      console.log(image.src);
      image.src = require("../assets/Index/2bacf8b2f1a848356e7acd8a59ddb1b.jpg");
      image.classList.add("flip");
    },
    mouseoutImg() {
      var image = document.getElementById("myImage");
      image.src = require("../assets/Index/recuritimg.jpg");
      image.classList.remove("flip");
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  display: flex;
  position: fixed;
  .title {
    width: 100vw;
    text-align: center;
    font-size: 2vw;
    position: absolute;
    background-image: linear-gradient(to right, #d7d2cc 0%, #304352 100%);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
  }
  .left {
    #myImage {
      width: 20vw;
      height: 30vw;
      position: fixed;
      left: 40%;
      top: 50%;
      translate:40%;
      background-size: cover;
      transition: transform 0.5s;
    }
    #imageContainer {
      position: relative;
      display: inline-block;
      perspective: 1000px;
    }
    .flip {
      transform:rotateY(180deg);
    }
  }
  .right {
    width: 40%;
    height: 60%;
    position: fixed;
    left: 60%;
    top: 50%;
    translate: -20% -50%;
    text-align: center;
    line-height: 60vh;
  }
}
</style>