<template>
  <div>
    <el-dialog
      :visible.sync="visible"
      :before-close="beforeClose"
      title="添加网址"
    >
      <el-form label-width="auto">
        <el-form-item label="请输入网址：">
          <el-input v-model="content" style="80%">
            <el-button @click="clickCopy()" slot="append">粘贴</el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="请输入网址详情：">
          <el-input
            v-model="title"
            @input="sensitiveWord()"
            type="textarea"
            rows="10"
          >
          </el-input>
          <span style="float: right">{{ title.length }}字</span>
        </el-form-item>
        <el-form-item label="分类：">
          <el-select
            name=""
            id=""
            v-model="classify"
            @change="classifyChoose()"
          >
            <el-option value="技术">技术</el-option>
            <el-option value="娱乐">娱乐</el-option>
            <el-option value="教育">教育</el-option>
            <el-option value="商业">商业</el-option>
            <el-option value="社交">社交</el-option>
            <el-option value="健身">健身</el-option>
            <el-option value="文化艺术">文化艺术</el-option>
            <el-option value="新闻媒体">新闻媒体</el-option>
          </el-select>
          <el-select
            name=""
            id=""
            v-model="classifyTechnology"
            v-if="technology"
          >
            <el-option value="JavaScript">JavaScript</el-option>
            <el-option value="Git">Git</el-option>
            <el-option value="Java">Java</el-option>
            <el-option value="Linux">Linux</el-option>
            <el-option value="Articles">Articles</el-option>
            <el-option value="C/C++">C/C++</el-option>
            <el-option value="phthon">phthon</el-option>
            <el-option value="php">php</el-option>
            <el-option value="MySQL">MySQL</el-option>
            <el-option value="安全">安全</el-option>
            <el-option value="攻防">攻防</el-option>
            <el-option value="资源" selected>资源</el-option>
            <el-option value="其它">其它</el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: space-around">
        <el-button @click="Reset()">重置</el-button>
        <el-button @click="AddShareUrl()" type="primary">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      title: "",
      content: "",
      technology: true,
      visible: true,
      classify: "技术",
      classifyTechnology: "资源",
    };
  },
  mounted() {},
  methods: {
    //回退路由
    beforeClose() {
      this.$router.back();
    },
    //用户点击确定发布网址
    AddShareUrl() {
      if (this.title == "" || this.content == "") {
        this.$message.warning({ message: "请输入" });
        return;
      }
      const reg1 = /^https?:\/\//gi;
      const reg2 = /^www\./gi;
      if (!reg1.test(this.content) && !reg2.test(this.content)) {
        this.$message.warning({ message: "请输入正确的网址" });
        return;
      }
      const id = localStorage.getItem("id");
      if (this.technology == true) {
        this.$axios
          .post("/ShareUrl/AddShareUrl", {
            id: id,
            content: this.content,
            title: this.title,
            classify: this.classify,
            classifyTechnology: this.classifyTechnology,
          })
          .then((res) => {
            if (res.data.code == "1") {
              this.beforeClose();
              this.$message.success({ message: "发布成功" });
              this.$bus.$emit("AddShareUrl");
            }
          });
      } else {
        this.$axios
          .post("/ShareUrl/AddShareUrl", {
            id: id,
            content: this.content,
            title: this.title,
            classify: this.classify,
          })
          .then((res) => {
            if (res.data.code == "1") {
              this.beforeClose();
              this.$message.success({ message: "发布成功" });
              this.$bus.$emit("AddShareUrl");
            }
          });
      }
    },
    //用户选择分类
    classifyChoose() {
      if (this.classify == "技术") {
        this.technology = true;
      } else {
        this.technology = false;
      }
    },
    //重置内容
    Reset() {
      this.title = "";
      this.content = "";
    },
    //点击粘贴用户剪切板内容
    async clickCopy() {
      //获取权限
      navigator.permissions
        .query({
          name: "clipboard-read",
        })
        .then((result) => {
          if (result.state == "granted" || result.state == "prompt") {
            //读取剪贴板
            navigator.clipboard.readText().then((text) => {
              this.content = text;
            });
          } else {
            this.$message.error({message:"剪贴板权限未开,请手动粘贴"})
          }
        });
      // const copy = await window.navigator.clipboard.readText();
      // this.content = copy;
    },
    //敏感词过滤
    sensitiveWord() {
      const reg =
        /他妈|你妈|操|傻逼|鸡巴|JB|脑残|恶心|废物|有病|无耻|废物|妈的|智障|死妈|傻鸟|操你妈|去死/gi;
      if (reg.test(this.title)) {
        this.title = this.title.replace(reg, "*");
      }
    },
  },
};
</script>
  
  <style lang="less" scoped>
</style>