<template>
  <div>
    <div class="box">
      <div class="top">
        <div class="add">
          <el-button
            type="primary"
            icon="el-icon-s-home
          "
            @click="ToAssociationIndex()"
          ></el-button>
        </div>
        <el-button
          style="background-color: bisque; margin-left: 1%"
          @click="AdminDeleteUserAll()"
          >批量删除</el-button
        >
        <div class="page">
          <el-select
            name=""
            v-model="skipskip"
            ref="changeSkip"
            @change="changeSkip()"
            id=""
          >
            <el-option
              :value="item"
              v-for="(item, index) in selectSkip"
              :key="index"
            >
              第{{ item }}页
            </el-option>
          </el-select>
          共{{ count }}条
        </div>
        <div class="search" @keyup.enter="AdminSearchUser()">
          <el-input v-model="searchUser" clearable placeholder="请输入姓名">
            <el-button slot="append" @click="AdminSearchUser()">搜索</el-button>
          </el-input>
        </div>
      </div>
      <div class="item">
        <table>
          <thead>
            <tr>
              <th></th>
              <th>姓名</th>
              <th>学院</th>
              <th>专业</th>
              <th>年级</th>
              <th>状态</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in user" :key="item.id">
              <td>
                <div class="checkbox">
                  <input
                    type="checkbox"
                    @click="AdminDeleteUserCount(item.id)"
                  />
                </div>
              </td>
              <td>
                <div class="username">{{ item.username }}</div>
              </td>
              <td>
                <div class="college">{{ item.college }}</div>
              </td>
              <td>
                <div class="major">{{ item.major }}</div>
              </td>
              <td>
                <div class="year">{{ item.year }}</div>
              </td>
              <td>
                <div class="status">{{ item.status }}</div>
              </td>
              <td>
                <div class="button">
                  <el-button
                    style="background-color: #333333; color: white"
                    @click="Edit(item.id)"
                    >编辑</el-button
                  >
                  <el-popconfirm
                    title="是否删除？"
                    @confirm="AdminDeleteUser(item.id)"
                  >
                    <el-button
                      style="background-color: #ffc200; color: black"
                      slot="reference"
                      >删除</el-button
                    >
                  </el-popconfirm>

                  <el-button
                    style="background-color: darkred; color: white"
                    @click="AddAdminUser(item.id)"
                    >添加</el-button
                  >
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchUser: "",
      user: [],
      skip: 0,
      selectSkip: 0,
      count: 0,
      DeleteList: [],
      id: "",
      skipskip: "1",
    };
  },
  mounted() {
    this.GetUserAll();
    this.$bus.$on("AdminOff", () => {
      this.GetUserAll();
    });
  },
  methods: {
    //回到主页
    ToAssociationIndex() {
      this.$router.replace("/MyUserInfo");
    },
    //处理添加管理员请求
    AddAdminUser(e) {
      this.$prompt("请输入超级管理员密码", "消息提示")
        .then((value) => {
          if (value == null) return;
          this.$axios
            .get(`/User/AddAdminUser?adminpassword=${value.value}&id=${e}`)
            .then(
              (res) => {
                if (res.data.code == "1") {
                  this.$message.success({ message: "添加成功" });
                } else {
                  this.$message.error({ message: "添加失败" });
                }
              },
              (err) => {
                console.log(err);
                this.$message.error({ message: "系统维护中..." });
              }
            );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //展示编辑
    Edit(e) {
      this.$router.push({
        path: "/AssociationAdmin/AdminRightUserEdit",
        query: {
          id: e,
        },
      });
    },
    //处理管理用批量删除用户
    AdminDeleteUserCount(e) {
      if (this.DeleteList.includes(e)) {
        this.DeleteList = this.DeleteList.filter((id) => id !== e);
      } else {
        this.DeleteList.push(e);
      }
    },
    AdminDeleteUserAll() {
      if (this.DeleteList.length == 0) return;
      this.$confirm("此操作不可逆！", "消息提示")
        .then(() => {
          this.$axios.post("/User/AdminDeleteUserAll", this.DeleteList).then(
            (res) => {
              if (res.data.code == "1") {
                this.$message.success({ message: "删除成功" });
                this.GetUserAll();
                this.DeleteList = [];
              } else {
                this.$message.error({ message: "删除失败" });
                this.DeleteList = [];
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //处理管理员删除用户
    AdminDeleteUser(e) {
      this.$confirm("此操作不可逆！", "消息提示")
        .then(() => {
          this.$axios.get("/User/AdminDeleteUser?id=" + e).then(
            (res) => {
              if (res.data.code == "1") {
                this.GetUserAll();
                this.$message.success({ message: "删除成功" });
              } else {
                this.$notify.error({
                  message: "管理员不得删除",
                  title: "消息警告",
                  position:"top-left"
                });
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //搜索用户
    AdminSearchUser() {
      if (this.searchUser == "" || this.searchUser == "\\") return;
      this.$axios
        .get("/User/AdminSearchUser?username=" + this.searchUser)
        .then((res) => {
          if (res.data.code == "1") {
            if (res.data.data.length != 0) {
              this.searchUser = "";
              this.user = res.data.data;
            } else if (res.data.data.length == 0) {
              this.$notify.warning({
                message: "未找找此用户",
                title: "消息提示",
                position: "top-left",
              });
            }
          }
        }),
        (err) => {
          this.$message.error({ message: "系统维护中..." });
          console.log(err);
        };
    },
    //翻页设置
    changeSkip() {
      this.skip = (this.skipskip - 1) * 10;
      this.GetUserAll();
    },
    //获取用户条数和首页用户10条信息
    GetUserAll() {
      this.$axios.get("/User/AllUser?skip=" + this.skip).then(
        (res) => {
          if (res.data.code == "1") {
            this.user = res.data.data;
            for (let i = 0; i < this.user.length; i++) {
              if (this.user[i].status == "0") {
                this.user[i].status = "未在线";
              } else {
                this.user[i].status = "在线";
              }
            }
          }
        },
        (err) => {
          this.$message.error({ message: "系统维护中..." });
          console.log(err);
        }
      );
      this.$axios.get("/User/UserCount").then((res) => {
        if (res.data.code == "1") {
          this.count = res.data.data;

          if (this.count / 10 == 1) {
            this.selectSkip = Math.floor(this.count / 10);
          } else {
            this.selectSkip = Math.floor(this.count / 10) + 1;
          }
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  width: 85vw;
  height: 89vh;
  overflow: auto;
  margin-left: 15vw;
  margin-top: -89vh;
  .top {
    display: flex;
    justify-content: space-around;
    margin: 10px;
    height: 6vh;
    width: 82vw;
  }
  .item {
    overflow: auto;
    table {
      width: 85vw;
      border-collapse: collapse;
      text-align: center;

      th,
      td {
        padding: 20px;
      }

      th {
        background-color: #f2f2f2;
        font-weight: bold;
      }
      td:last-child {
        width: 20vw;
      }
    }
  }
}
</style>